.cases {

    main {
        overflow: hidden;
    }

    .back__link {
        @include tablet {
            top: 35px;
        }

        @include tabletLow {
            left: 20px;
        }
    }

    &__intro {
        .container {
            position: relative;
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
        }

        &--headlines {

            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            align-items: flex-start;

            span {
                display: inline-flex;
                margin-right: 96px;
                font-family: $gt-reg;
                font-weight: 500;
                line-height: 46px;
                font-size: 24px;
                letter-spacing: 0.03em;
                color: $dark;

                @include tabletMedium {
                    font-size: 20px;
                    line-height: 35px;
                }

                &:last-of-type {
                    margin-right: 0;
                }
            }
        }

        &--info {
            width: calc(100% - 620px - 13%);
            margin-right: 13%;

            @include tabletMedium {
                width: calc(100% - 350px - 10%);
                margin-right: 10%;
            }

            @include tabletLow {
                width: 100%;
                margin: 0px auto 32px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;
            }
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            padding-top: 1.5em;
            margin-bottom: 50px;
            width: 100%;

            @include tabletMedium {
                font-size: 48px;
                line-height: 68px;
            }

            @include tablet {
                padding-top: 1.75em;
            }

            @include tabletLow {
                font-size: 32px;
                line-height: 45px;
                padding-top: 2.75em;
                margin-bottom: 10px;
            }
        }

        &--description {
            max-width: 620px;
            width: 100%;
            padding-right: 4em;

            @include tabletMedium {
                max-width: 350px;
                padding-right: 0;
            }

            @include tabletLow {
                max-width: 100%;
            }


            p {
                font-family: $font-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;

                @include tabletLow {
                    line-height: 28px;
                }

                b,
                strong {
                    font-family: $font-bold;
                    color: $udark;
                }
            }
        }

        &--image {
            max-width: 934px;
            width: 100%;
            min-height: 336px;
            margin-top: 70px;
            margin-bottom: 70px;
            margin-left: calc(100% - 934px);
            background-size: cover;
            background-repeat: no-repeat;
            background-position: top center;
            position: relative;

            @include tabletMedium {
                margin: 60px auto 70px;
                min-height: 290px;
            }

            @include tabletLow {
                margin: 40px auto;
                min-height: 250px;
            }
        }
    }

    &__content {
        padding-bottom: 6em;

        @include tabletLow {
            padding-bottom: 4em;
        }

        &--orange,
        blockquote {
            margin-left: calc(8.5% + 200px);
            width: calc(100% - 8.5% - 200px);
            padding: 2.5em 6.4em;
            background-color: $accent;
            margin-top: 50px;

            p {
                max-width: 100%;
                font-family: $gt-reg;
                font-weight: 500;
                line-height: 35px;
                font-size: 20px;
                letter-spacing: 0.03em;
                color: $white;
                margin-bottom: 19px;

                @include tabletLow {
                    font-size: 16px;
                    line-height: 26px;
                }

            }

            @include tabletMedium {
                margin-left: 0px;
                width: 100%;
            }

            @include tabletLow {
                padding: 1.5em;
            }

            span.quote__author {
                font-family: $font-light;
                line-height: 26px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $white;

                @include tabletLow {
                    font-size: 14px;
                    line-height: 20px;
                }
            }
        }

        &--item {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-wrap: wrap;
            margin-bottom: 120px;

            @include tabletMedium {
                margin-bottom: 72px;
            }

            @include tabletLow {
                margin-bottom: 40px;
            }

            &:last-of-type {
                @include tabletLow {
                    margin-bottom: 0;
                }
            }

            p {
                max-width: 415px;
                width: 100%;
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 18px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 30px;
                margin-right: 8.35%;

                a {
                    color: $dark;
                    text-decoration: none;
                    transition: color 0.4s;

                    &:hover {
                        color: $accent;
                    }
                }

                @include tabletMedium {
                    max-width: 100%;
                    margin: 0px auto 30px;
                }

                @include tabletLow {
                    line-height: 28px;
                }


                &:last-of-type {
                    margin-right: 0;
                    margin-bottom: 0;
                }
            }

            h2,
            h3,
            h4,
            h5,
            h6 {
                font-family: $font-bold;
                line-height: 56px;
                font-size: 40px;
                letter-spacing: 0.03em;
                color: $dark;

                @include tabletLow {
                    line-height: 37px;
                    font-size: 26px;
                }

            }

            &.cases__content--alternative {

                &:last-of-type {
                    margin-bottom: 60px;

                    @include tabletLow {
                        margin-bottom: 20px;
                    }
                }

                h2,
                h3,
                h4,
                h5,
                h6 {
                    font-family: $gt-reg;
                    font-weight: 500;
                    line-height: 46px;
                    font-size: 24px;
                    letter-spacing: 0.03em;
                    color: $dark;
                }

                .cases__content--orange {
                    p {
                        max-width: 100%;
                        font-family: $gt-reg;
                        font-weight: 500;
                        line-height: 35px;
                        font-size: 20px;
                        letter-spacing: 0.03em;
                        color: $white;
                        margin-bottom: 19px;

                        @include tabletLow {
                            font-size: 16px;
                            line-height: 26px;
                        }

                    }
                }


            }
        }

        &--title {
            max-width: 200px;
            width: 100%;
            margin-right: 8.5%;

            @include tabletLow {
                max-width: 100%;
                margin: 0px 0px 24px 0px;
            }
        }

        &--text {
            width: calc(100% - 8.5% - 200px);
            display: flex;


            @include tabletMedium {
                flex-direction: column;
            }

            @include tabletLow {
                width: 100%;
            }

            img {
                display: block;
                max-width: 250px;
                max-height: 250px;
                width: 100%;
                margin-left: 18%;
                position: relative;
                top: -30px;

                @include tabletMedium {
                    top: 0;
                    margin: 30px 0px 0px 0px;
                }

            }
        }

    }

    &__link {
        background: $dark;
        padding: 6.25em 0em;

        @include tabletLow {
            padding: 5em 0em;
        }

        .container {
            position: relative;
            min-height: 81px;
        }
    }


}
