.portfolios {
    background: $dark;
    position: relative;
    min-height: 90vh;

    &:before {
        content: '';
        display: block;
        width: 819px;
        height: 232px;
        position: absolute;
        right: 0;
        top: 0px;
        background: url('../img/contact/contacts-bg.svg');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: top center;
        z-index: 0;
        display: none;

        @include desktopLow {
            top: 0px;
            display: block;
        }

        @include tabletMedium {
            width: 100%;
        }
    }

    &__intro {
        @include desktopLow {
            padding-top: 4em;
        }

        @include tabletMedium {
            padding-top: 1.5em;
        }

        @include tablet {
            padding-top: 0;
        }

        .container {
            position: relative;
        }

        .back__link {
            span {
                color: $white;
            }

            path {
                fill: $white;
            }

            @include desktopLow {
                top: 0px;
            }

            @include tabletMedium {
                top: 30px;
            }

            @include tablet {
                top: 46px;
            }



            @include tabletLow {
                top: 35px;
                left: 20px;
            }
        }

        h1 {
            max-width: 300px;
            width: 100%;
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $white;
            padding-top: 1.25em;

            @include tabletMedium {
                line-height: 68px;
                font-size: 48px;
                padding-top: 2.25em;
            }

            @include tabletLow {
                font-size: 32px;
                line-height: 45px;
                padding-top: 3em;
            }
        }
    }

    &__main {
        .container {
            position: relative;
        }
    }

    &__menu {
        position: absolute;
        width: 150px;
        right: 85px;
        top: -97px;
        z-index: 55;

        @include desktopLow {
            z-index: 20;
        }

        @include tablet {
            right: 30px;
        }

        @include tabletLow {
            position: relative;
            width: 100%;
            right: 0;
            top: 0;
            margin-top: 20px;
            margin-bottom: 20px;
        }

        ul {
            width: 100%;
        }

        li {
            width: 100%;
            font-family: $gt-reg;
            font-weight: 500;
            line-height: 35px;
            font-size: 20px;
            letter-spacing: 0.03em;
            color: $white;
            transition: color 0.4s;
            cursor: pointer;
            margin-bottom: 16px;

            @include tabletMedium {
                font-size: 18px;
                line-height: 31px;
            }

            &:hover {
                color: $accent;
            }

            &.active {
                color: $accent;
            }

            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    &__tab {
        display: none;
        position: relative;
        top: 50px;
        left: -80px;
        width: calc(100% - 143px);
        margin-right: 143px;
        padding-bottom: 9em;
        opacity: 0;

        @include desktop {
            left: -30px;
        }

        @include tabletMedium {
            padding-bottom: 7em;
        }

        @include tabletLow {
            left: 0;
            top: 0;
            width: 100%;
            margin: 0;
            padding-bottom: 2.5em;
        }



        &:first-of-type {
            display: block;
            opacity: 1;
        }

        &.is-active-tab {
            opacity: 1;
            transition: opacity 0.3s;
        }
    }

    &__inner {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;

        @include desktopLow {
            flex-direction: column;
        }

        &.single {
            justify-content: flex-end;
            align-items: flex-start;

            @include tabletMedium {
                justify-content: flex-start
            }

            .portfolios__item:nth-of-type(odd) {
                margin-top: 0;
                top: -50px;
                left: -40px;

                @include desktop {
                    left: -80px;
                }

                @include desktopLow {
                    left: 0px;
                    margin-left: 32%;
                }

                @include tabletMedium {
                    top: 0;
                    margin-left: 15%;
                }

                @include tabletLow {
                    margin: 0px auto;
                }
            }
        }
    }

    &__item {
        width: calc(100%/2 - 104px);
        margin-right: 104px;
        height: 350px;
        position: relative;
        text-decoration: none;
        color: $white;

        &:nth-of-type(odd) {
            margin-right: 0px;
            top: -50px;
            left: calc(100% - 470px);

            @include desktop {
                left: calc(100% - 530px);
            }

            @include desktopLow {
                left: calc(100% - 470px);
                top: 0;
            }

            @include tabletMedium {
                left: 0;
                margin-left: 22%;
            }

            @include tablet {
                left: 0;
                margin-left: 17%;
            }

            @include tabletLow {
                margin: 0px auto 32px;
            }
        }

        &:nth-of-type(even) {
            margin-top: 250px;
            right: calc(100% - 640px);

            @include desktopLow {
                right: 0;
                margin-top: 0%;
            }

            @include tablet {
                margin-left: 15%;
            }

            @include tabletLow {
                margin: 0px auto 32px;
            }
        }

        @include desktopLow {
            width: 372px;
            height: 277px;
            margin-right: 0;
            margin-bottom: 32px;
        }

        @include tabletLow {
            width: 288px;
            height: 213px;
        }

        &:after {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            background-color: rgba(0, 0, 0, 0.4);
            transition: all .4s;
            opacity: 1;
            z-index: 0;
        }

        &--info {
            position: absolute;
            left: 40px;
            bottom: 56px;
            z-index: 5;
            opacity: 1;
            transition: all 0.4s;
            width: 85%;

            @include tabletLow {
                left: 16px;
                bottom: 16px;
            }

            p {
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;

                @include tabletLow {
                    font-size: 20px;
                    line-height: 33px;
                }
            }

            span {
                display: block;
                font-family: $gt-reg;
                line-height: 38px;
                font-size: 20px;

                @include tabletLow {
                    font-size: 16px;
                    line-height: 28px;
                }
            }
        }

        &:hover .portfolios__item--info {
            transform: translateX(40px);
            opacity: 0;
        }

        &:hover::after {
            opacity: 0;
        }


    }
}

.page-id-165 .header.dark:before,
.page-id-1036 .header.dark:before {
    right: 50%;
    transform: translate(56%, 0%);

    @include desktopLow {
        display: none;
    }
}
