.blog {
    &__wrapp {
        position: relative;
        min-height: calc(100vh - 144px - 105px);

        @include desktopLow {
            min-height: calc(100vh - 90px - 151px);
        }

        @include tablet {
            min-height: calc(100vh - 90px - 255px);
        }

        @include tabletLow {
            min-height: calc(100vh - 90px - 206px);
        }

        @include mobile {
            min-height: calc(100vh - 68px - 206px);

        }





    }

    &__intro {
        .container {
            position: relative;
            padding-top: 5em;

            @include tabletMedium {
                padding-top: 5em;
            }
        }

        .back__link {
            left: 35px;
            top: 15px;

            @include desktopLow {
                top: 33px;
            }

            @include tablet {
                top: 30px;
                left: 25px;
            }
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;

            @include tabletMedium {
                line-height: 68px;
                font-size: 48px;
            }

            @include tabletLow {
                line-height: 45px;
                font-size: 32px;
            }
        }

        &--bg {
            width: 830px;
            height: 280px;
            position: absolute;
            z-index: 0;
            left: 64.5%;
            top: -143px;
            transform: translate(-55%, 0%);
            background: url('../img/q&a/q&a-bg.svg');
            background-size: cover;
            background-repeat: no-repeat;



            @include desktopLow {
                left: 70%;
            }

            @include tabletMedium {
                top: -87px;
                left: 85%;
                width: 100%;
                height: 220px;
            }

            @include tabletLow {
                top: -100px;
                left: 0;
                transform: translate(10%, 0%);
            }
        }
    }

    &__list--posts {
        position: relative;

    }

    &__year {
        padding-top: 4em;
        padding-bottom: 10px;

        &:first-child {
            @include tabletLow {
                padding-top: 2em;
            }
        }

        &:nth-child(even) {
            background: $dark;
            color: $white;
        }

        &:nth-child(odd) {
            background: $white;
            color: $dark;
        }

        .container {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            position: relative;

            @include tabletMedium {
                flex-direction: column;
            }
        }

        &--name {
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: inherit;
            max-width: 330px;
            width: 100%;
            margin-right: 60px;

            @include tabletMedium {
                max-width: 100%;
                margin-bottom: 24px;
            }

            @include tabletLow {
                line-height: 37px;
                font-size: 26px;
            }
        }

        &--list {
            display: flex;
            width: calc(100% - 390px);
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
            position: relative;
            padding-top: 20px;

            @include tabletMedium {
                width: 100%;
                padding-left: 15%;
            }

            @include tabletLow {
                padding-left: 0;
            }
        }

    }

    &__item {
        display: flex;
        width: 100%;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: 62px;
        position: relative;

        @include tabletLow {
            margin-bottom: 32px;
            flex-direction: column;
        }

        &--date {
            max-width: 110px;
            width: 100%;
            margin-right: 20px;
            font-family: $font-reg;
            line-height: 30px;
            font-size: 18px;
            letter-spacing: 0.03em;
            color: inherit;

            @include tabletLow {
                max-width: 100%;
                line-height: 26px;
                font-size: 16px;
            }
        }

        &--title {
            width: calc(100% - 130px);
            font-family: $font-med;
            line-height: 34px;
            font-size: 24px;
            letter-spacing: 0.03em;
            color: inherit;

            @include tabletLow {
                width: 100%;
                line-height: 25px;
                font-size: 18px;
            }

            a {
                text-decoration: none;
                color: inherit;
                transition: color 0.4s;

                &:hover {
                    color: $accent;
                }
            }
        }

        &--thumbnail {
            width: 467px;
            height: 276px;
            position: absolute;
            left: 0;
            top: 0;
            transform: translate(-80%, -50%);
            background-size: cover;
            background-repeat: no-repeat;
            z-index: 20;
            display: none;

            @include tabletMedium {
                left: -1000%;
                opacity: 0;
            }
        }
    }
}


.blog__article {

    &--intro {

        .back__link {
            left: 35px;

            @include tabletMedium {
                top: -5px;
            }

            @include tablet {
                top: 30px;
                left: 25px;
            }
        }

        .container {
            position: relative;
            padding-top: 4em;

            @include tabletMedium {
                padding-top: 3em;
            }

            @include tablet {
                padding-top: 5.5em;
            }
        }

        p {
            font-family: $font-reg;
            font-size: 18px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 20px;
        }

        h1 {
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 50px;

            @include tabletMedium {
                line-height: 68px;
                font-size: 48px;
            }

            @include tabletLow {
                line-height: 37px;
                font-size: 26px;
                margin-bottom: 32px;
            }
        }

    }

    &--bg {
        max-width: 826px;
        width: 100%;
        min-height: 336px;
        background-size: cover;
        background-repeat: no-repeat;
        margin: 0px auto 50px;

        @include tabletMedium {
            min-height: 290px;
        }

        @include tabletLow {
            min-height: 240px;
            margin: 0px auto 40px;
        }
    }

    &--inner {
        max-width: 730px;
        width: 100%;
        margin: 0px auto;
    }

    &--content {
        padding-bottom: 50px;

        p {
            max-width: 620px;
            width: 100%;
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 18px;
            letter-spacing: 0.03em;
            color: $dark;
            margin: 0 auto 20px;

            @include tabletMedium {
                max-width: 100%;
                padding-left: 15%;
            }

            @include tabletLow {
                padding-left: 0;
            }

            a {

                color: $accent;
                transition: color 0.4s;

                &:hover {
                    color: $hovered;
                }

            }

            b,
            strong {
                font-weight: 400;
                color: $accent;
                text-decoration: underline;
            }
        }

        img {
            display: block;
            max-width: 826px;
            height: auto;
            margin: 30px auto 50px;

            @include tabletMedium {
                max-width: 100%;
            }

            @include tabletLow {
                margin: 30px auto 30px;
            }

        }

        figure {
            display: block;
            max-width: 826px;
            height: auto;
            margin: 30px auto 50px;

            img {
                margin-bottom: 20px;
            }

            @include tabletMedium {
                max-width: 100%;
            }

            @include tabletLow {
                margin: 30px auto 30px;
            }

            figcaption {
                width: 100%;
                margin-bottom: 50px;
                font-family: $font-reg;
                line-height: 26px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;
            }
        }

        h2 {
            max-width: 620px;
            width: 100%;
            font-family: $font-bold;
            line-height: 34px;
            font-size: 30px;
            letter-spacing: 0.03em;
            color: $dark;
            margin: 0px auto 20px;

            @include tabletMedium {
                max-width: 100%;
                padding-left: 15%;
            }

            @include tabletLow {
                padding-left: 0;
                font-size: 20px;
                line-height: 28px;
            }
        }

        h3,
        h4,
        h5,
        h6 {
            max-width: 620px;
            width: 100%;
            font-family: $gt-reg;
            font-weight: 500;
            line-height: 35px;
            font-size: 22px;
            letter-spacing: 0.03em;
            color: $dark;
            margin: 20px auto 13px;

            @include tabletMedium {
                max-width: 100%;
                padding-left: 15%;
            }

            @include tabletLow {
                padding-left: 0;
                font-size: 18px;
                line-height: 22px;
            }
        }

        ul {
            list-style: none;
            list-style-image: none;
            max-width: 620px;
            width: 100%;
            margin: 20px auto 20px;

            @include tabletMedium {
                max-width: 100%;
                padding-left: 15%;
            }

            @include tabletLow {
                padding-left: 0;
            }

            li {
                font-family: $gt-reg;
                line-height: 28px;
                font-size: 18px;
                letter-spacing: 0.03em;
                color: $dark;
                position: relative;
                padding-left: 25px;

                a {
                    color: $accent;
                    transition: color 0.4s;

                    &:hover {
                        color: $hovered;
                    }
                }

                &:before {
                    content: '';
                    display: block;
                    width: 5px;
                    height: 5px;
                    background-color: $dark;
                    border-radius: 50%;
                    position: absolute;
                    left: 0;
                    top: 13px;
                }
            }
        }

        ol {
            max-width: 620px;
            width: 100%;
            margin: 0px auto 20px;
            list-style: none;
            list-style-image: none;
            counter-reset: item;

            @include tabletMedium {
                max-width: 100%;
                padding-left: 15%;
            }

            @include tabletLow {
                padding-left: 0;
            }

            li {
                font-family: $gt-med;
                font-weight: 500;
                line-height: 28px;
                font-size: 18px;
                letter-spacing: 0.03em;
                color: $dark;
                position: relative;
                list-style-type: none;

                @include tabletLow {
                    padding-left: 20px;
                }

                a {
                    color: $accent;
                    transition: color 0.4s;

                    &:hover {
                        color: $hovered;
                    }
                }


                &:before {
                    content: counter(item);
                    counter-increment: item;
                    color: $accent;
                    font-family: $font-med;
                    line-height: 26px;
                    font-size: 16px;
                    position: absolute;
                    left: -20px;
                    top: 1px;

                    @include tabletLow {
                        left: 0;
                    }
                }
            }
        }


    }

    blockquote {
        max-width: 826px;
        width: 100%;
        margin: 40px auto 50px;
        padding: 30px 15px;
        background-color: $accent;

        p {
            max-width: 620px;
            width: 100%;
            line-height: 38px;
            font-size: 20px;
            color: $white;
            margin: 0 auto;
        }
    }
}

.single-post {
    .video {
        max-width: 620px;
        flex-direction: column;
        margin: 50px auto 50px;

        @include tabletLow {
            margin: 50px auto 20px;
        }

        &__block {
            width: 100%;

            img.video__block--media {
                height: 100%;
                margin: 0px;
            }
        }

        &__text {
            width: 100%;
        }
    }
}
