.music__intro {


    &--inner {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;
        flex-wrap: wrap;

        @include tabletMedium {
            min-height: 625px;
        }

        @include tabletLow {
            min-height: 475px;
        }
    }


    &--text {
        width: calc(100% - 700px);
        margin-right: 0px;

        @include desktop {
            width: calc(100% - 675px);
        }

        @include desktopLow {
            width: calc(100% - 500px);
        }

        @include tabletLow {
            position: relative;
        }

        h1 {
            @include desktopLow {
                font-size: 50px;
                line-height: 56px;
            }
        }
    }

    &--image {
        min-height: 516px;
        width: 700px;

        @include desktop {
            width: 650px;
        }

        @include desktopLow {
            width: 475px;
        }

        @include tabletMedium {
            max-width: 480px;
            width: 100%;
            min-height: 500px;
            position: absolute;
            right: -15px;
            bottom: 0;
        }

        @include tabletLow {
            position: relative;
            right: 0;
            max-width: 100%;
        }

        canvas#newWave,
        canvas#musicWave {
            display: block;
            width: 95%;
            height: 300px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 4;

            @include mobile {
                height: 225px;
            }
        }

        canvas#newLineWaves {
            display: block;
            position: absolute;
            top: 78.8%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 4;

            @include tabletMedium {
                top: 79.8%;
            }

            @include tabletLow {
                top: 90.5%;
            }

            @include mobile {
                top: 91.3%;
            }
        }

        img {
            position: absolute;
            display: block;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            z-index: 15;
            max-height: 516px;

            @include desktopLow {
                max-width: 100%;
            }


        }
    }

    &--image.fields-intro__image {
        padding-top: 0;

        @include tabletLow {
            min-height: 370px;
            margin-bottom: 60px;
        }

        @include mobile {
            min-height: 275px;
        }

        img {
            @include tabletLow {
                max-width: 450px;
                width: 100%;
                top: 60%;
            }
        }
    }

    &--line {
        width: 100%;
        height: 2px;
        left: 0;
        bottom: 107px;
        position: absolute;
        background-image: linear-gradient(to right, #ffe0dc, #ff8271, #ff634e, #ff634e, #ffe0dc);

        @include tabletMedium {
            bottom: 99px;
        }

        @include tabletLow {
            bottom: 95px;
        }

        @include mobile {
            bottom: 84px;
        }
    }
}


.music__increase {

    .container {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        padding-top: 8em;
        padding-bottom: 8em;

        @include tabletMedium {
            padding-top: 0em;
            padding-bottom: 2em;
            flex-direction: column;
        }

        @include mobile {
            padding-top: 3.8em;
            padding-bottom: 1em;
        }

        &:before {
            content: '';
            display: block;
            width: 400%;
            height: 100%;
            background-color: $dark;
            position: absolute;
            right: 30px;
            top: 0;
            z-index: 1;

            @include desktop {
                right: 0;
            }
        }
    }

    &--inner {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        width: 100%;
        position: relative;
        z-index: 5;

        @include tabletMedium {
            flex-direction: column;
            flex-wrap: wrap;
        }

        h3 {
            max-width: 420px;
            margin-right: 80px;
            width: 100%;
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: $white;

            @include tabletMedium {
                margin-bottom: 35px;
            }

            @include tabletLow {
                line-height: 37px;
                font-size: 26px;
                margin-bottom: 32px;
            }
        }
    }

    &--text {
        width: calc(100% - 530px);

        @include tabletMedium {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-end;
            flex-direction: column;
        }

        p {
            font-family: $font-bold;
            line-height: 40px;
            font-size: 30px;
            letter-spacing: 0.03em;
            color: $accent;
            margin-bottom: 10px;

            @include tabletLow {
                line-height: 33px;
                font-size: 20px;
                margin-bottom: 20px;
            }
        }

        span {
            display: block;
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $white;
            margin-bottom: 47px;

            &:last-of-type {
                margin-bottom: 0;

                @include tabletLow {
                    margin-bottom: 40px;
                }
            }

            @include tabletLow {
                line-height: 28px;
                margin-bottom: 30px;
            }
        }
    }

}

.anti .music__scan .scan__item {
    width: calc(100%/3);

    @include tabletLow {
        width: 100%;
    }
}

.anti .music__scan .container {

    h3,
    h4,
    h5,
    h6 {
        @include tabletLow {
            line-height: 37px;
            font-size: 26px;
        }
    }

    @include tabletLow {
        padding-left: 15px;
        padding-bottom: 5.5em;
    }
}
