.marketing {
    .container {
        position: relative;
    }

    .back__link {
        left: 35px;

        @include desktopLow {
            top: 46px;
        }

        @include tablet {
            top: 30px;
            left: 25px;
        }
    }

    &__main {
        position: relative;
        display: flex;
        flex-wrap: wrap;

        &-info {
            width: calc(100% - 600px);
            padding-right: 10px;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            padding-top: 10px;

            @media (max-width: 1199px) {
                width: 70%;
                padding: 0;
            }
        }

        &-inner {
            margin: auto 0;
        }

        @media (max-width: 1199px) {
            padding-top: 118px;
        }

        @media (max-width: 767px) {
            padding-top: 92px;

            &-info {
                width: 100%;
            }
        }
    }

    &__back {
        line-height: 30px;
        font-size: 18px;
        letter-spacing: 0.03em;
        color: #000;
        opacity: .5;
        display: flex;
        align-items: center;
        text-decoration: none;
        transition: all .3s ease;
        position: absolute;
        top: -25px;
        left: 0;

        span {
            margin-left: 20px;
        }

        &:hover {
            opacity: 1;
        }

        @media (max-width: 1199px) {
            top: 49px;
            left: -2px;
        }

        @media (max-width: 767px) {
            top: 33px;
            left: 0;
            font-size: 16px;

            span {
                margin-left: 16px;
            }
        }
    }

    &__bg {
        width: 590px;
        height: 530px;
        position: relative;
        display: flex;
        justify-content: center;
        align-content: center;

        img {
            display: block;
            max-width: 100%;
            margin: 0px auto;
        }



        @media (max-width: 1199px) {
            margin: -85px 0 0 auto;
            width: 412px;
            height: 412px;

            &-item {
                &:nth-child(2) {
                    max-width: 300px;
                    max-height: 300px;
                }

                &:nth-child(3) {
                    max-width: 230px;
                    max-height: 230px;
                }

                &:nth-child(4) {
                    top: 96px;
                    max-width: 27px;
                    max-height: 27px;
                }

                &:nth-child(5) {
                    top: 16px;
                    right: 100px;
                    max-width: 19px;
                    max-height: 19px;
                }

                &:nth-child(6) {
                    top: calc(50% + 101px);
                    left: calc(50% + 62px);
                    max-width: 7px;
                    max-height: 7px;
                }
            }

            &-img {
                &1 {
                    max-width: 123%;
                }

                &2 {
                    top: calc(50% - 100px);
                    left: calc(50% + 42px);
                    max-width: 40%;
                }

                &3 {
                    top: calc(50% - 30px);
                    left: calc(50% + 47px);
                    max-width: 50%;
                }
            }
        }

        @media (max-width: 767px) {
            margin: 56px auto 0;
            max-width: 290px;
            max-height: 290px;

            &-item {
                &:nth-child(2) {
                    max-width: 220px;
                    max-height: 220px;
                }

                &:nth-child(3) {
                    max-width: 160px;
                    max-height: 160px;
                }

                &:nth-child(4) {
                    max-width: 19px;
                    max-height: 19px;
                    left: 10px;
                    top: 63px;
                }

                &:nth-child(5) {
                    top: 6px;
                    right: 72px;
                    max-width: 13px;
                    max-height: 13px;
                }

                &:nth-child(6) {
                    top: calc(50% + 65px);
                    left: calc(50% + 42px);
                    max-width: 5px;
                    max-height: 5px;
                }
            }

            &-img {
                &1 {
                    max-width: 124%;
                    top: calc(50% - 6px);
                }

                &2 {
                    top: calc(50% - 80px);
                    left: calc(50% + 29px);
                }
            }
        }
    }

    &__title {
        font-family: $font-bold;
        line-height: 79px;
        font-size: 56px;
        letter-spacing: 0.03em;
        color: $dark;

        @media (max-width: 1199px) {
            line-height: 68px;
            font-size: 48px;
        }

        @media (max-width: 767px) {
            line-height: 45px;
            font-size: 32px;
            max-width: 100%;
        }
    }

    &__subtitle {
        font-family: $gt-reg;
        line-height: 28px;
        font-size: 16px;
        letter-spacing: 0.03em;
        color: $dark;
        margin-top: 32px;

        @media (max-width: 1199px) {
            margin-top: 41px;
        }

        @media (max-width: 767px) {
            margin-top: 31px;
        }
    }

    &__info {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        margin-top: 76px;

        &-item {
            padding: 37px 40px 50px;
            width: 50%;
            background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
        }

        &-title {
            font-family: $gt-reg;
            line-height: 35px;
            font-size: 20px;
            letter-spacing: 0.03em;
            color: #F45B46;
            max-width: 80%;
        }

        &-description {
            font-family: $gt-reg;
            margin-top: 20px;
            line-height: 28px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;
        }

        @media (max-width: 1199px) {
            margin-top: 42px;
            margin-right: -25px;
            width: calc(100% + 25px);

            &-item {
                padding: 34px 20px 18px 25px;
            }

            &-title {
                line-height: 35px;
                font-size: 20px;
                max-width: 100%;
            }

            &-description {
                br {
                    display: none;
                }
            }
        }

        @media (max-width: 767px) {
            margin-top: 35px;

            &-item {
                padding: 30px 15px 39px 40px;
                width: 100%;
                background: linear-gradient(360deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 159.96%);
            }

            &-description {
                margin-top: 15px;
                line-height: 27px;
            }
        }
    }

    &__communicate {
        background: $dark;
        padding: 95px 60px 100px 0;
        position: relative;
        display: flex;
        flex-wrap: wrap;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            right: 99%;
            bottom: 0;
            width: 200%;
            background: $dark;
            z-index: -1;

        }

        &-col {
            &:first-child {
                width: 35%;
            }

            &:last-child {
                width: 54.6%;
                margin-left: auto;
                margin-right: 20px;
            }
        }

        &-title {
            font-family: $font-med;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: #FFFFFF;
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: #FFFFFF;

            &:not(:last-child) {
                margin-bottom: 27px;
            }
        }

        @media (max-width: 1199px) {
            padding: 77px 25px 105px 0;
            margin-right: -25px;

            &-col {
                &:first-child {
                    width: 100%;
                }

                &:last-child {
                    width: 100%;
                    margin-left: auto;
                    margin-top: 40px;
                    padding-left: 100px;
                    margin-right: 0;
                }
            }

            &-title {
                line-height: 56px;
                font-size: 40px;
                max-width: 690px;
            }

            p {
                line-height: 28px;
            }
        }

        @media (max-width: 767px) {
            padding: 57px 25px 85px 0;

            &-title {
                line-height: 37px;
                font-size: 26px;
                max-width: 100%;
            }

            &-col {
                &:last-child {
                    margin-top: 27px;
                    padding-left: 0;
                }
            }

            p {
                line-height: 26px;
                font-size: 16px;
                letter-spacing: .02em;

                &:not(:last-child) {
                    margin-bottom: 17px;
                }
            }
        }
    }

    &__show {
        padding: 147px 0 150px;

        &-title {
            font-family: $font-med;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.035em;
            color: $dark;
            position: relative;
            z-index: 10;
        }

        &-tabs {
            display: flex;
            // flex-wrap: wrap;
            width: 100%;
            padding-left: 102px;
            margin-top: -100px;

            @include tablet {
                flex-wrap: wrap;
            }
        }

        &-thumb {
            width: 100vw;
            height: 100vh;
            max-width: 620px;
            max-height: 620px;
            display: flex;
            flex-wrap: wrap;
            position: relative;
        }

        &-image {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            &._product {
                position: absolute;
                top: calc(50% + 18px);
                left: calc(50% - 10px);

                .marketing__show {
                    &-shadow {
                        margin-top: -29px;
                        margin-left: 96px;
                    }
                }
            }

            &._media {
                position: absolute;
                top: calc(50% + 48px);
                left: calc(50%);

                .marketing__show {
                    &-shadow {
                        margin-top: -109px;
                        margin-left: 60px;
                    }
                }

                &._active {
                    ~.marketing__show-dot {
                        &._first {
                            top: 77.1%;
                            left: 41.8%;
                        }

                        &._second {
                            top: 10%;
                            right: 39%;
                        }

                        &._third {
                            bottom: 58%;
                            right: 82.6%;
                        }
                    }
                }
            }

            &._updates {
                .marketing__show {
                    &-shadow {
                        margin-left: 48px;
                        margin-top: -130px;
                        position: relative;
                        top: -20px;
                    }
                }

                &._active {
                    ~.marketing__show-dot {
                        &._first {
                            top: 59.1%;
                            left: 72.8%;
                        }

                        &._second {
                            top: 8.7%;
                            right: 41%;
                        }

                        &._third {
                            bottom: 35.7%;
                            right: 85.6%;
                        }
                    }
                }
            }
        }

        &-nav {
            width: calc(100% - 620px);
            padding: 119px 0 0 103px;

            &-item {
                &:not(:first-child) {
                    margin-top: 20px;
                }

                &._active {
                    .marketing__show-nav {
                        &-name {
                            color: $accent;
                        }
                    }
                }
            }

            &-name {
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                letter-spacing: 0.03em;
                color: $dark;
                cursor: pointer;
                transition: all .3s ease;

                &:hover {
                    color: $accent;
                }
            }

            &-subnav {
                margin-top: 17px;
                margin-bottom: 40px;
            }

            &-subitem {
                line-height: 28px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;

                &:not(:first-child) {
                    margin-top: 12px;
                }
            }
        }

        &-bg {
            background: $accent;
            width: 100%;
            height: 100%;
            border-radius: 50%;
        }

        &-dot {
            transition: all .3s ease-in-out;

            &._first {
                position: absolute;
                top: 49.6%;
                left: 10%;
                width: 36px;
                height: 36px;
                background: #F5F5F5;
                border-radius: 50%;
            }

            &._second {
                position: absolute;
                top: 18.9%;
                right: 29%;
                width: 26px;
                height: 26px;
                background: #F5F5F5;
                border-radius: 50%;
            }

            &._third {
                position: absolute;
                bottom: 34%;
                right: 19.7%;
                width: 10px;
                height: 10px;
                background: $dark;
                border-radius: 50%;
            }
        }

        @media (max-width: 1199px) {
            padding: 75px 0 30px;

            &-title {
                line-height: 68px;
                font-size: 48px;

                br {
                    display: none;
                }
            }

            &-tabs {
                margin-top: 40px;
                padding: 0;
            }

            &-thumb {
                max-width: 312px;
                max-height: 312px;
                margin-top: 16px;
            }

            &-nav {
                width: calc(100% - 312px);
                padding: 0 0 0 60px;

                &-subnav {
                    margin-bottom: 37px;
                }

                &-subitem {
                    &:not(:first-child) {
                        margin-top: 10px;
                    }
                }
            }

            &-image {
                img {
                    max-width: 110%;
                }

                &._product {
                    position: absolute;
                    top: calc(50% + 12px);
                    left: calc(50% - 15px);

                    .marketing__show {
                        &-shadow {
                            margin-top: -29px;
                            margin-left: 36px;
                        }
                    }
                }

                &._media {
                    position: absolute;
                    top: calc(50% - 2px);
                    left: calc(50%);

                    .marketing__show {
                        &-shadow {
                            margin-top: -109px;
                            margin-left: 10px;
                        }
                    }
                }

                &._updates {
                    top: calc(50% + 12px);
                    left: calc(50% - 9px);

                    .marketing__show {
                        &-shadow {
                            margin-left: 7px;
                            max-width: 98%;
                        }
                    }
                }
            }

            &-dot {
                transition: all .3s ease-in-out;

                &._first {
                    position: absolute;
                    top: 49.6%;
                    left: 10%;
                    width: 18px;
                    height: 18px;
                    background: #F5F5F5;
                    border-radius: 50%;
                }

                &._second {
                    position: absolute;
                    top: 18.9%;
                    right: 29%;
                    width: 14px;
                    height: 14px;
                    background: #F5F5F5;
                    border-radius: 50%;
                }

                &._third {
                    position: absolute;
                    bottom: 34%;
                    right: 19.7%;
                    width: 5px;
                    height: 5px;
                    background: $dark;
                    border-radius: 50%;
                }
            }
        }

        @media (max-width: 767px) {
            padding: 58px 0 21px;

            &-title {
                line-height: 45px;
                font-size: 32px;
                letter-spacing: 0.03em;
            }

            &-bg {
                max-width: 290px;
                max-height: 290px;
            }

            &-tabs {
                margin-top: 19px;
            }

            &-nav {
                width: 100%;
                padding: 13px 0 0;

                &-name {
                    line-height: 33px;
                    font-size: 20px;
                }

                &-subnav {
                    margin-top: 22px;
                    margin-bottom: 30px;
                }

                &-item {
                    &:not(:first-child) {
                        margin-top: 32px;
                    }
                }

                &-subitem {
                    &:not(:first-child) {
                        margin-top: 8px;
                    }
                }
            }
        }
    }

    &__lifestyle {
        display: flex;
        flex-wrap: wrap;

        &-item {
            width: 33%;
            display: inline-flex;
            flex-wrap: wrap;
            align-items: center;
            padding: 100px 50px;

            &._mini {
                width: 25%;
                padding: 95px 20px 100px 0;
            }

            &._standard {
                width: 42%;
                background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
            }

            &._bg {
                position: relative;

                .marketing__lifestyle-inner {
                    margin: 0 -36% auto 0;
                }

                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: -27.1%;
                    bottom: 0;
                    background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
                    z-index: 1;
                }
            }
        }

        &-inner {
            margin: 0 auto auto 0;
        }

        &-heading {
            font-family: $font-med;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: $dark;
        }

        &-title {
            font-family: $font-med;
            line-height: 34px;
            font-size: 24px;
            letter-spacing: 0.03em;
            color: $dark;
            max-width: 70%;
            position: relative;
            z-index: 5;
        }

        &-description {
            font-family: $gt-reg;
            line-height: 28px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-top: 50px;
            max-width: 70%;
            position: relative;
            z-index: 5;
        }

        @media (max-width: 1199px) {
            &-item {
                &._mini {
                    width: 100%;
                    padding: 95px 20px 44px 0;

                    .marketing__lifestyle-inner {
                        max-width: 410px;
                    }
                }

                &._standard {
                    width: 52%;
                }

                &._bg {
                    width: 48%;
                }
            }

            &-title {
                max-width: 100%;
            }

            &-description {
                margin-top: 35px;
                max-width: 280px;
            }
        }

        @media (max-width: 767px) {
            &-title {
                line-height: 25px;
                font-size: 18px;
            }

            &-description {
                line-height: 26px;
                font-size: 16px;
                letter-spacing: .01em;
                margin-top: 12px;
            }

            &-item {
                width: 33%;
                display: inline-flex;
                flex-wrap: wrap;
                align-items: center;
                padding: 100px 50px;

                &._mini {
                    width: 100%;
                    padding: 40px 0;

                    .marketing__lifestyle {
                        &-heading {
                            line-height: 37px;
                            font-size: 26px;
                        }

                        &-inner {
                            max-width: 90%;
                        }
                    }
                }

                &._standard {
                    width: calc(100% + 15px);
                    margin-right: -15px;
                    padding: 38px 15px 38px 40px;
                    background: linear-gradient(360deg, #f5f5f5 0%, rgba(245, 245, 245, 0) 159.96%);

                    .marketing__lifestyle-title {
                        max-width: 68%;
                        letter-spacing: 0.05em;
                    }
                }

                &._bg {
                    width: calc(100% + 15px);
                    margin-right: -15px;
                    padding: 38px 15px 38px 40px;
                    background: linear-gradient(360deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 159.96%);

                    .marketing__lifestyle-inner {
                        margin: 0 -36% auto 0;
                    }

                    .marketing__lifestyle-title {
                        max-width: 68%;
                        letter-spacing: 0.05em;
                    }

                    &:before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: -27.1%;
                        bottom: 0;
                        background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
                        z-index: 1;
                    }
                }
            }
        }
    }

    &__where {
        background: $dark;
        position: relative;
        padding: 150px 0 166px;

        &-title {
            font-family: $font-med;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: #FFFFFF;
        }

        &-description {
            line-height: 28px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: #FFFFFF;
            margin-top: 22px;
        }

        &-row {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            width: 100%;

            &:not(:first-child) {
                margin-top: 57px;
            }
        }

        &-main {
            width: 50%;
            padding-right: 50px;
            padding-bottom: 32px;
        }

        &-map {
            width: 50%;
            padding: 33px 0;
            position: relative;
            margin-top: 32px;
        }

        &-thumb {
            width: 55%;
            padding: 90px calc(5% + 10px) 90px 0;
            text-align: center;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                background: url('../img/scan&buy.svg') no-repeat center;
                z-index: 0;
                transform: translate(-53px, 30px);
            }

            &-1 {
                position: relative;
                z-index: 2;
            }

            &-2 {
                position: relative;
                z-index: 2;
                margin-left: 50px;
                top: 80px;
            }

            .header__btn {
                display: none;
            }
        }

        &-info {
            width: 40%;
            margin-left: auto;
            margin-right: 20px;
            padding-top: 110px;

            .header__btn {
                margin-top: 60px;
                height: 45px;
                width: 176px;
            }
        }

        &-product {
            &-1 {
                position: absolute;
                top: 27%;
                left: 19%;
            }

            &-2 {
                position: absolute;
                top: -8.8%;
                left: 46.9%;
            }

            &-3 {
                position: absolute;
                top: 10.1%;
                left: 62.2%;
            }
        }

        @media (max-width: 1199px) {
            padding: 75px 0 62px;

            &-main {
                width: 100%;
                padding: 0;
            }

            &-description {
                margin-top: 42px;
                max-width: 470px;
            }

            &-map {
                width: 100%;
                padding: 27px 0 33px 53px;
                position: relative;
                margin-top: 37px;
            }

            &-thumb {
                padding: 90px calc(5% + 30px) 90px 0;

                &:before {
                    transform: translate(-21px, 25px);
                    max-width: 370px;
                    max-height: 370px;
                    background-size: cover;
                }

                &-1 {
                    max-width: 133px;
                }

                &-2 {
                    max-width: 133px;
                    margin-left: 20px;
                    top: 50px;
                }
            }

            &-row {
                &:not(:first-child) {
                    margin-top: -74px;
                }
            }

            &-info {
                padding-top: 160px;
                margin-left: -25px;
                margin-bottom: -11px;
                margin-right: -40px;
                width: 44%;

                .marketing__where-description {
                    margin-top: 28px;
                    max-width: 370px;
                }

                .header__btn {
                    margin-top: -9px;
                    margin-left: 0;
                    opacity: 1;
                    margin-bottom: 110px;
                }
            }

            .header__socials {
                display: none;
            }
        }

        @media (max-width: 767px) {
            padding: 57px 0 42px;

            &-title {
                line-height: 37px;
                font-size: 26px;
                letter-spacing: 0.03em;
            }

            &-description {
                line-height: 26px;
                font-size: 16px;
                letter-spacing: 0.03em;
                margin-top: 28px;
            }

            &-map {
                padding: 0;
                max-width: 290px;
                margin: 67px auto 0;

                img {
                    max-width: 103%;
                    margin-left: -17px;
                }
            }

            &-product {
                &-1 {
                    top: 22%;
                    left: 20%;
                    max-width: 77px !important;
                }

                &-2 {
                    top: -23%;
                    left: 52%;
                    max-width: 95px !important;
                }

                &-3 {
                    top: -5%;
                    left: 66%;
                    max-width: 135px !important;
                }
            }

            &-row {
                &:not(:first-child) {
                    margin-top: 0;
                }
            }

            &-info {
                width: 100%;
                margin: 56px 0 0;
                padding: 0;
                order: 1;
                top: 0;

                .header__btn {
                    display: none;
                }
            }

            &-thumb {
                width: 100%;
                order: 2;
                padding: 0;
                max-width: 290px;
                margin: 76px auto 0;

                &:before {
                    transform: translate(0, 25px);
                }

                &-1 {
                    max-width: 130px;
                }

                &-2 {
                    max-width: 130px;
                }

                .header__btn {
                    display: initial;
                    margin-top: 102px;
                    width: 167px;
                    opacity: 1;
                    margin-bottom: 77px;
                }
            }
        }
    }

    &__targeted {
        padding: 100px 0 154px;

        &-container {
            display: flex;
            flex-wrap: wrap;
        }

        &-col {
            width: 50%;
            display: inline-flex;
            flex-wrap: wrap;
            align-items: center;
        }

        &-title {
            font-weight: bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.05em;
            color: $dark;
        }

        &-thumb {
            width: 50%;
            min-height: 340px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            position: relative;

            .item {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }

        &-dots {
            width: 100%;
            display: flex;
            margin-top: 47px;
        }

        &-dot {
            cursor: pointer;
            width: 100%;
            position: relative;
            padding-top: 48px;
            padding-left: 15px;

            &:after {
                content: '';
                position: absolute;
                top: 18px;
                left: 20px;
                right: 6px;
                height: 2px;
                background: url("../img/marketing/dots.svg") repeat-x;
                z-index: -1;
            }

            &:nth-child(1) {
                .marketing__targeted-name {
                    max-width: 165px;
                }
            }

            &:nth-child(2) {
                .marketing__targeted-name {
                    max-width: 210px;
                }
            }



            &._active {
                .marketing__targeted {
                    &-line {
                        max-width: 100%;
                        transition: all 5s linear;
                    }

                    &-icon {
                        &:after {
                            background: #FF634E;
                            box-shadow: 0 0 0 12px rgba(#FF634E, .3), 0 0 0 5px rgba(#FF634E, .6);
                        }
                    }
                }
            }
        }

        &-name {
            display: block;
            max-width: 160px;
            width: 100%;
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: #000000;
        }

        &-icon {
            position: absolute;
            top: 14px;
            left: 23px;
            width: 10px;
            height: 10px;

            &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: rgba($dark, .7);
                box-shadow: 0 0 0 12px rgba($dark, .1), 0 0 0 5px rgba($dark, .3);
                border-radius: 50%;
                z-index: 3;
                transition: all .3s ease;
            }

            &:before {
                content: '';
                position: absolute;
                top: -24px;
                bottom: -24px;
                left: -24px;
                right: -24px;
                background: #fff;
                border-radius: 50%;
                z-index: 0;
            }
        }

        &-line {
            position: absolute;
            height: 2px;
            top: 18px;
            left: 60px;
            right: 6px;
            background: #FF634E;
            border-radius: 2px;
            max-width: 0;
        }

        @media (max-width: 1199px) {
            padding: 77px 0 117px;

            &-col {
                width: 100%;
            }

            &-thumb {
                margin-top: 46px;
                width: 62%;
            }

            &-dots {
                margin-top: 30px;
            }
        }

        @media (max-width: 767px) {
            padding: 56px 0 87px;

            &-title {
                line-height: 37px;
                font-size: 26px;
            }

            &-thumb {
                width: 100%;
                margin-top: 0;
                min-height: 325px;

                img {
                    max-width: calc(100vw - 30px);
                }
            }

            &-dots {
                flex-wrap: wrap;
                margin-top: 10px;
            }

            &-dot {
                padding: 0 0 0 50px;

                &:after {
                    content: '';
                    position: absolute;
                    top: 18px;
                    bottom: -40px;
                    left: 18px;
                    height: auto;
                    width: 2px;
                    background: url("../img/marketing/dots-vertical.svg") repeat-y;
                    z-index: -1;
                }

                &:not(:last-child) {
                    margin-bottom: 40px;
                }

                &:last-child {
                    &:after {
                        display: none;
                    }

                    .marketing__targeted {
                        &-line {
                            display: none;
                        }
                    }
                }

                &._active {
                    .marketing__targeted {
                        &-line {
                            max-width: none;
                            max-height: 100%;
                            transition: all 5s linear;
                        }
                    }
                }
            }

            &-name {
                line-height: 26px;
                font-size: 16px;
                letter-spacing: 0.03em;
            }

            &-icon {
                top: 9px;
                left: 13px;

                &:before {
                    top: -14px;
                    bottom: -14px;
                }
            }

            &-line {
                position: absolute;
                left: 18px;
                right: initial;
                background: #FF634E;
                border-radius: 2px;
                max-width: none;
                top: 36px;
                width: 2px;
                bottom: -32px;
                height: initial;
                max-height: 0;
            }
        }
    }

    &__technology {
        background: $dark;

        &-title {
            font-family: $font-bold;
            line-height: 49px;
            font-size: 30px;
            letter-spacing: 0.03em;
            color: #FFFFFF;
            padding-right: 65px;

            @media (max-width: 1199px) {
                padding-right: 0px;
            }
        }

        &-left {
            width: 41.8%;
            display: inline-flex;
            flex-wrap: wrap;
            align-items: center;
            padding-bottom: 20px;
        }

        &-right {
            display: inline-flex;
            flex-wrap: wrap;
            align-items: center;
            background: $accent;
            width: 250%;
            padding: 77px 0 72px;
            margin-right: -191.8%;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 100%;
                bottom: 0;
                background: $accent;
                width: 250%;
            }

            @media (min-width: 1200px) {
                background: url('../img/marketing/line-horizontal.svg') 0 calc(50% - 48px) repeat-x $accent;

                &:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    top: 0;
                    background: url('../img/marketing/line-horizontal.svg') 0 calc(50% + 48px) repeat-x;
                    z-index: 1;
                }
            }
        }

        &-img {
            &--center {
                margin-left: -73px;
                border: 15px solid $dark;
                border-radius: 50%;
                width: 113px;
                height: 113px;
            }

            &:not(:first-child) {
                margin-left: 35px;
                position: relative;
                top: 13px;
            }

            @media (min-width: 1200px) {
                position: relative;
                z-index: 2;
            }
        }

        .container {
            display: flex;
            flex-wrap: wrap;
        }

        @media (max-width: 1199px) {
            &-left {
                width: 100%;
                padding: 70px 0;
            }

            &-right {
                width: 100%;
                padding: 77px 0 50px;
                margin-right: 0;
                position: relative;
                margin-left: 60px;
            }

            &-title {
                max-width: 80%;

                br {
                    display: none;
                }
            }

            &-img {
                &--center {
                    margin-left: -63px;
                    border: 13px solid $dark;
                    border-radius: 50%;
                    max-width: 121px;
                    margin-top: -26px;
                }

                &:not(:first-child) {
                    margin-left: 22px;
                    position: relative;
                    top: 0;
                    max-width: 32%;
                    max-height: 160px;
                }
            }
        }

        @media (max-width: 767px) {
            &-title {
                line-height: 33px;
                font-size: 20px;
                letter-spacing: 0.03em;
                max-width: 100%;
            }

            &-left {
                padding: 59px 0 92px;
            }

            &-right {
                margin-left: 0;
                margin-right: 0;
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                flex-direction: column;
                padding: 0 0 57px;

                &:before {
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    top: 0;
                    background: url('../img/marketing/line.svg') calc(50% - 48px) 0 no-repeat;
                    width: 100%;
                    z-index: 1;
                }

                &:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    top: 0;
                    background: url('../img/marketing/line.svg') calc(50% + 48px) 0 no-repeat;
                    z-index: 1;
                }
            }

            &-img {
                position: relative;
                z-index: 2;

                &--center {
                    margin-left: 0;
                    margin-top: -62px;
                    margin-bottom: 29px;
                    border: 13px solid $dark;
                    border-radius: 50%;
                    max-width: 120px;
                    max-height: 120px;
                }

                &:not(:first-child) {
                    max-width: 100%;
                    max-height: 160px;
                    margin: 0;
                }
            }
        }
    }

    &__dignity {
        background: $dark;
        padding: 100px 0 112px;

        &-item {
            width: 25%;
        }

        &-img {
            height: 70px;
        }

        &-title {
            display: block;
            margin-top: 16px;
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0;
            color: #FFFFFF;
            max-width: 60%;
        }

        .container {
            display: flex;
            flex-wrap: wrap;
        }

        @media (max-width: 1199px) {
            padding: 120px 0 112px;

            &-item {
                width: 33.3333%;

                margin-top: 58px;
            }

            &-title {
                max-width: 80%;
                margin-top: 18px;
            }

            .container {
                margin-top: -58px;
            }
        }

        @media (max-width: 1199px) {
            padding: 80px 0;

            &-item {
                width: 100%;
                margin-top: 32px;
                padding-left: 24px;
            }

            &-title {
                max-width: 80%;
                margin-top: 18px;
                line-height: 26px;
            }

            .container {
                margin-top: -32px;
            }
        }
    }

    .main {
        overflow: hidden;
    }

    @media (max-width: 1199px) {
        .main {
            overflow: hidden;

            .container {
                padding: 0 25px;
            }
        }
    }

    @media (max-width: 767px) {
        .main {
            .container {
                padding: 0 15px;
            }
        }
    }
}
