.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 3.2em 0em;
    background-color: transparent;
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 50;



    @include desktopLow {
        padding: 2em 0em;
        background-color: $white;
        border-bottom: 1px solid #DADADA;
    }

    @include mobile {
        padding: 1.3em 0em;
    }

    &.dark {
        background-color: $dark;

        &:before {
            content: '';
            display: block;
            width: 819px;
            height: 232px;
            position: absolute;
            right: 0;
            top: 0px;
            background: url('../img/contact/contacts-bg.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: top center;
            z-index: 0;

            @include desktopLow {
                top: 90px;
            }

            @include mobile {
                top: 65px;
            }
        }

        .header__hamburger--label span {
            background: $white;
        }

        .header__logo div {
            color: $white;
        }

        .header__menu {
            @include desktopLow {
                background: $dark url(../img/main/header-bg.svg);
            }
        }


        .header__menu a {
            color: $white;
        }

        .header__socials .header__socials--item {
            color: $white;
            border-color: rgba(255, 255, 255, 0.1);
        }
    }

    &__inner {
        max-width: 1240px;
        width: 100%;
        margin: 0px auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;

        @include desktopLow {
            padding: 0px 20px;

        }
    }



    &__logo {
        a {
            text-decoration: none;
            display: flex;
            justify-content: flex-start;
            align-items: center;

            &:focus {
                outline: none;
                border: none;
            }
        }

        div:first-of-type {
            margin-right: 30px;
        }

        div {
            color: $dark;
        }
    }

    &__menu {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        @include desktopLow {
            position: fixed;
            flex-direction: column;
            justify-content: center;
            opacity: 0;
            left: 0;
            top: 90px;
            height: calc(100vh - 89px);
            width: 100%;
            z-index: 200;
            background: $white url('../img/popups/preloader-bg.svg');
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            transform: translateX(-100%);
            transition: opacity .35s ease-in-out, transform .1s linear 0.5s;

        }

        @include mobile {
            top: 68px;
            height: calc(100vh - 50px);
        }


        nav {
            margin-right: 25px;

            @include desktopLow {
                width: 100%;
                margin: 0px auto 25px;
                padding-top: 1em;
            }

            @include mobile {
                padding-top: 0;
            }
        }

        ul {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            @include desktopLow {
                justify-content: center;
                flex-direction: column;
            }

            a {
                font-family: $font-reg;
                text-decoration: none;
                font-weight: 500;
                line-height: normal;
                font-size: 12px;
                letter-spacing: 0.07em;
                text-transform: uppercase;
                color: $dark;
                position: relative;
                transition: all 0.4s;



                @include mobile {
                    font-size: 16px;
                }


                &:before {
                    content: '';
                    display: block;
                    width: 0%;
                    height: 2px;
                    background-color: $accent;
                    position: absolute;
                    bottom: -10px;
                    left: 0;
                    transition: width 0.4s;
                }

                &:hover,
                &.is-active {
                    color: $accent;

                    &:before {
                        width: 100%;
                    }
                }

            }
        }

        li {
            display: block;
            margin-right: 37px;

            @include desktopLow {
                margin: 0px auto 40px;
                opacity: 0;
                transform: translateY(40px);
                transition: opacity .15s linear, transform .15s linear;
            }

            @include mobile {
                margin: 0px auto 35px;
            }
        }

        li:last-of-type {
            margin-right: 0px;

            @include desktopLow {
                margin: 0px auto 0px;
            }
        }



    }





    &__btn {
        width: 143px;
        height: 40px;
        background: $accent;
        font-family: $font-bold;
        font-size: 12px;
        text-align: center;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: $white;
        border-radius: 100px;
        transition: all 0.4s;

        @include desktopLow {
            display: block;
            margin: 0px auto 40px;
            opacity: 0;
            transform: translateY(40px);
            transition: opacity .15s linear, background 0.4s, transform .15s linear;
        }

        &:hover {
            background: $hovered;
        }
    }

    &__socials {
        position: absolute;
        right: 0;
        top: 85px;
        display: flex;
        align-items: center;
        flex-direction: column;

        @include desktopLow {
            max-width: 280px;
            width: 100%;
            margin: 0px auto;
            position: relative;
            flex-direction: row;
            top: 0;
            justify-content: center;
        }

        &--item {
            width: 30px;
            height: 30px;
            margin-bottom: 28px;
            background-color: transparent;
            border: 1px solid rgba(46, 46, 46, 0.1);
            border-radius: 50%;
            text-align: center;
            line-height: 28px;
            font-size: 14px;
            text-decoration: none;
            transition: all 0.4s;
            color: $some;

            @include desktopLow {
                margin-right: 10px;
                margin-left: 10px;
                margin-bottom: 0px;
                opacity: 0;
                transform: translateY(40px);
                transition: opacity .15s linear, color 0.4s, background-color 0.4s, border-color 0.4s, transform .15s linear;
            }

            &:hover,
            &:focus {
                color: $white;
                background-color: $accent;
                border-color: $accent;
            }
        }

        .icon-linkedin {
            padding-left: 2px;
            line-height: 26px;
        }

        .icon-twitter {
            font-size: 12px;
            padding-left: 2px;
        }

        .icon-instagram {
            font-size: 13px;
            padding-left: 1px;
        }

        &--item:last-of-type {
            margin-bottom: 0;
        }
    }





    &__hamburger {

        &--nav {
            display: none;
        }

        &--nav:checked~.header__menu {
            transform: translateX(0%);
            opacity: 1;
            transition: opacity .25s linear;
        }

        &--nav:checked~.header__menu .select2 {
            opacity: 1;
            transform: translateY(0);
            transition: opacity .3s ease-in-out .3s, transform .3s ease-in-out .3s;
        }

        &--nav:checked~.header__menu li {
            opacity: 1;
            transform: translateY(0);
            transition: opacity .3s ease-in-out .3s, transform .3s ease-in-out .3s;
        }

        &--nav:checked~.header__menu .header__btn {
            opacity: 1;
            transform: translateY(0);
            transition: opacity .3s ease-in-out .3s, transform .3s ease-in-out .3s;
        }

        &--nav:checked~.header__menu .header__socials--item {
            opacity: 1;
            transform: translateY(0);
            transition: opacity .3s ease-in-out .3s, transform .3s ease-in-out .3s;
        }

        &--label {
            width: 24px;
            height: 18px;
            display: none;
            position: absolute;
            top: 50%;
            right: 20px;
            transform: translateY(-50%) rotate(0deg);
            transition: .5s ease-in-out;
            cursor: pointer;
            z-index: 150;

            @include desktopLow {
                display: block;
            }

            span {
                display: block;
                position: absolute;
                height: 0.15rem;
                width: 100%;
                background: $dark;
                opacity: 1;
                left: 0;
                transform: rotate(0deg);
                transition: .25s ease-in-out;

                @include mobile {
                    height: 0.10rem;
                }

                &:nth-of-type(1) {
                    top: 0;
                }

                &:nth-of-type(2) {
                    top: 8px;
                    transition-property: transform, opacity;
                }

                &:nth-of-type(3) {
                    top: 16px;
                }
            }

            &.open {

                span {
                    left: -2px;

                    &:nth-of-type(1) {
                        transform: translate3d(0, 10px, 0) rotate(45deg);
                    }

                    &:nth-of-type(2) {
                        opacity: 0;
                        transform: translateX(-10px);


                    }

                    &:nth-of-type(3) {
                        transform: translate3d(0, -6px, 0) rotate(-45deg);
                    }

                }


            }
        }
    }

}

.select2 {
    margin-right: 27px;

    @include desktopLow {
        margin: 0px auto 30px;
        opacity: 0;
        transform: translateY(40px);
        transition: opacity .15s linear, transform .15s linear;
    }
}

.select2-container .select2-selection--single .select2-selection__rendered {
    height: 40px;
    padding: 0;
    font-family: $font-med;
    font-size: 12px;
    line-height: 40px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $dark;
    transition: all 0.4s;
    text-align: center;

    &:hover {
        color: $accent;
    }
}

.header.dark .select2-container .select2-selection--single .select2-selection__rendered {
    color: $white;

    &:hover {
        color: $accent;
    }
}


.select2-container .select2-selection--single {
    height: auto;
    border: 1px solid rgba(46, 46, 46, 0.1);
    border-radius: 0px;
    transition: all 0.4s;

    &:hover {
        background: #ffefed;
        border-color: #ffefed;
    }
}

.header.dark .select2-container .select2-selection--single {
    border-color: $white;
    background-color: $dark;

    &:hover {
        border-color: $accent;
    }
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    display: none;
}

.select2-search.select2-search--dropdown {
    display: none;
}

.select2.select2-container--open {
    .select2-selection--single {
        background: #ffefed;
        border-color: $accent;

        .select2-selection__rendered {
            color: $accent;
        }
    }
}

.header.dark .select2.select2-container--open {
    .select2-selection--single {
        background: $dark;
        border-color: $accent;

        .select2-selection__rendered {
            color: $accent;
        }
    }
}

.select2-dropdown {
    border: 1px solid #FF634E;
    border-radius: 0px;
    transform: translateY(-1px);
}

.dark.select2-dropdown {
    background-color: $dark;
}

.select2-results__option {
    height: 40px;
    line-height: 40px;
    padding: 0;
    color: $dark;
    font-family: $font-med;
    font-size: 12px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    transition: all 0.4s;
    text-align: center;
    background: $white;
}

.dark .select2-results__option {
    color: $white;
    background: $dark;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
    background: #ffefed;
    color: $dark;
}

.select2-container--default .dark .select2-results__option--highlighted[aria-selected] {
    background: $dark;
    color: $accent;
}

.select2-container--default .select2-results__option[aria-selected=true] {
    background-color: $white;
    color: $accent;
}

.select2-container--default .dark .select2-results__option[aria-selected=true] {
    background-color: $dark;
    color: $accent;
}
