.tagging__wrapp {
    background: #F5F5F5;
    min-height: 100vh;

    &--intro {
        .container {
            position: relative;
            padding-top: 4.5em;

            @include tabletMedium {
                padding-top: 3.5em;
            }

            @include tabletLow {
                padding-top: 5.5em;
            }

            &:last-child {
                @include tabletLow {
                    padding-top: 1em;
                }
            }
        }

        .back__link {
            left: 35px;

            @include tabletMedium {
                top: -5px;
            }

            @include tablet {
                top: 30px;
                left: 25px;
            }
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 100px;

            @include tabletMedium {
                line-height: 68px;
                font-size: 48px;
                margin-bottom: 0px;
            }

            @include tabletLow {
                line-height: 37px;
                font-size: 26px;
                margin-bottom: 32px;
            }
        }

        .main-controls {
            position: absolute;
            right: 6.5em;
            top: 4.5em;

            @include tabletMedium {
                display: none;
            }
        }


    }

    &--slider {
        width: 100%;
        padding-bottom: 85px;

        @include tabletMedium {
            padding-bottom: 60px;
        }

        @include tabletLow {
            padding-bottom: 40px;
        }

        .swiper-slide {
            width: 375px;
            margin-right: 62px;

            @include tabletMedium {
                width: 100%;
                margin: 0px auto 32px;
            }

            &:last-of-type {
                margin-right: 0;

                @include tabletMedium {
                    margin: 0px auto 32px;
                }
            }
        }

        &.swiper-container {
            @include tabletLow {
                overflow: initial;
            }
        }

        .swiper-wrapper {
            // transition: all 0.2s !important;

            @include tabletMedium {
                box-sizing: border-box;
                flex-direction: column;
            }
        }

        &__opening {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(0%, -50%);
            width: 16px;
            height: 112px;
            background: url('../img/technologies/opened.png');
            background-size: cover;
            background-repeat: no-repeat;
            z-index: 20;
            transition: opacity 0.4s;
            opacity: 0;

            @include tabletMedium {
                width: 92px;
                height: 13px;
                background: url('../img/technologies/opened-adapt.png');
                background-size: cover;
                top: 98%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            &:before {
                content: '';
                display: block;
                width: 12px;
                height: 12px;
                background: url('../img/technologies/arrow-left.svg');
                background-size: cover;
                background-repeat: no-repeat;
                position: absolute;
                left: 60%;
                top: 50%;
                transform: translate(-50%, -50%);

                @include tabletMedium {
                    width: 13px;
                    height: 13px;
                    background: url('../img/technologies/down-arrow.svg');
                    background-size: cover;
                    left: 52.5%;
                    top: 40%;
                }
            }
        }

        &__item {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            position: relative;
            border-radius: 5px;
            height: 350px;
            cursor: pointer;

            @include tabletMedium {
                height: auto;
                flex-direction: column;
            }

            &:hover {
                .tagging__wrapp--slider__main {
                    background: $accent;
                }

                .tagging__wrapp--slider__main p,
                .tagging__wrapp--slider__main ul li {
                    color: $white;
                }

                .tagging__wrapp--slider__main ul li:before {
                    background-color: $white;
                }

                .tagging__wrapp--slider__opening {
                    opacity: 1;
                }
            }

            &.is-opened {

                .tagging__wrapp--slider__main {
                    background: $accent;
                    border-radius: 5px 0px 0px 5px
                }

                .tagging__wrapp--slider__main p,
                .tagging__wrapp--slider__main ul li {
                    color: $white;
                }

                .tagging__wrapp--slider__main ul li:before {
                    background-color: $white;
                }

                .tagging__wrapp--slider__opening {
                    opacity: 1;
                }
            }
        }

        &__main {
            height: 100%;
            width: 375px;
            padding: 32px;
            position: relative;
            border-radius: 5px;
            background: $white;
            transition: background 0.4s;

            @include tabletMedium {
                width: 100%;
                padding: 40px;
            }

            @include tabletLow {
                padding: 24px 16px;
            }

            p {
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 25px;
                margin-top: -20px;
                transition: color 0.4s;

                @include tabletMedium {
                    margin-top: 6px;

                }

                @include tabletLow {
                    font-size: 22px;
                    line-height: 26px;
                    padding-left: 20px;
                    padding-right: 70px;
                }
            }

            ul {
                list-style: none;
                list-style-image: none;

                li {
                    font-family: $gt-reg;
                    font-weight: 500;
                    line-height: 30px;
                    font-size: 16px;
                    letter-spacing: 0.03em;
                    color: $dark;
                    margin-bottom: 15px;
                    position: relative;
                    padding-left: 24px;
                    transition: color 0.4s;

                    @include tabletLow {
                        line-height: 22px;
                        margin-bottom: 10px;
                    }

                    &:before {
                        content: '';
                        display: block;
                        width: 4px;
                        height: 4px;
                        border-radius: 50%;
                        background-color: $accent;
                        position: absolute;
                        left: 0;
                        top: 13px;
                        transition: background-color 0.4s;
                    }
                }
            }
        }

        &__image {
            display: flex;
            position: relative;
            width: 90px;
            height: 90px;
            background: $white;
            box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
            border-radius: 3px;
            margin-left: calc(100% - 90px);

            @include tabletMedium {
                position: absolute;
                right: 40px;
                top: 40px;
            }

            @include tabletLow {
                right: 16px;
                top: -20px;
                width: 64px;
                height: 64px;
            }

            img {
                display: block;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                max-width: 100%;

                @include tabletLow {
                    max-width: 36px;
                    width: 100%;
                }

            }

        }

        &__description {
            width: calc(100% - 3% - 375px);
            display: none;
            height: 100%;
            padding: 30px 38px 20px 40px;
            background: $white;
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;
            position: relative;
            overflow: hidden;

            @include tabletMedium {
                width: 100%;
                height: auto;
                padding: 53px 35px;
                overflow: initial;
            }

            @include tabletLow {
                padding: 45px 10px 45px 16px;
            }

            h2,
            h3,
            h4,
            h5,
            h6 {
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 19px;
                opacity: 0;

                @include tabletMedium {
                    opacity: 1;
                }

                @include tabletLow {
                    font-size: 20px;
                    line-height: 33px;
                }
            }

            p,
            div {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;
                opacity: 0;
                max-height: 173px;
                overflow: hidden;

                @include tabletMedium {
                    opacity: 1;
                    overflow: initial;
                    max-height: 100%;
                }

                @include tabletLow {
                    line-height: 28px;
                }
            }

        }
    }
}

.page-id-344,
.page-id-701 {
    background-color: #f5f5f5;
}

.page-id-701 .tagging__wrapp--slider__item {
    height: 370px;

    @include tabletMedium {
        height: auto;
        flex-direction: column;
    }
}

.page-id-701 .tagging__wrapp--slider__description div,
.tagging__wrapp--slider__description p {
    font-size: 15px;
    line-height: 28px;
}

.page-id-701 .tagging__wrapp--slider__main ul li {
    margin-bottom: 5px;

    @include tabletMedium {
        margin-bottom: 15px;
    }

    @include tabletLow {
        margin-bottom: 10px;
    }
}
