.partner {
    background: $dark;
    position: relative;

    .container {
        position: relative;
    }

    .back__link {
        span {
            color: $white;
        }

        path {
            fill: $white;
        }

        top: -20px;

        @include desktopLow {
            top: 0px;
        }

        @include tablet {
            top: 45px;
        }

        @include tabletLow {
            top: 40px;
            left: 20px;
        }
    }

    &__intro {
        padding-top: 16px;

        @include desktopLow {
            padding-top: 60px;
        }

        @include tablet {
            padding-top: 15px;
        }

        @include mobile {
            padding-top: 5px;
        }

        h1 {
            width: 91%;
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $white;
            padding-top: 1.25em;
            padding-bottom: 0.2em;
            border-bottom: 5px solid $white;

            @include desktopLow {
                width: 100%;
            }

            @include tabletMedium {
                font-size: 48px;
                line-height: 68px;
            }

            @include tablet {
                padding-top: 2.5em;
            }

            @include tabletLow {
                font-size: 32px;
                line-height: 45px;
                padding-top: 3.2em;
            }
        }
    }

    &__main {
        padding-top: 6.25em;

        @include tabletMedium {
            padding-top: 5em;
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        padding-right: 8.5%;
        padding-left: 16%;

        @include desktopLow {
            padding-right: 0;
            padding-left: 0;
        }
    }

    &__item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: 100px;

        @include tabletMedium {
            margin-bottom: 52px;
        }

        @include tabletLow {
            flex-direction: column;
        }

        &--image {
            width: 151px;

            @include tabletLow {
                margin-bottom: 32px;
            }

            img {
                display: block;
                max-width: 100%;
                max-height: 35px;
            }
        }

        &--text {
            width: calc(100% - (13.5% + 151px));
            margin-left: 13.5%;

            @include tabletMedium {
                width: 100%;
                margin: 0;
            }

            p {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: #8c8c8c;

                @include tabletLow {
                    line-height: 28px;
                }
            }
        }
    }
}

.page-id-183 {
    .header.dark:before {
        right: 50%;
        transform: translate(56%, 0%);

        @include desktopLow {
            right: 0;
            width: 100%;
            transform: translate(0%, 0%);
        }
    }
}
