.contacts {
    background: $dark;
    height: 100%;

    &__back {
        line-height: 30px;
        font-size: 18px;
        letter-spacing: 0.03em;
        color: #FFFFFF;
        opacity: .5;
        display: flex;
        align-items: center;
        text-decoration: none;
        margin-bottom: 27px;

        span {
            margin-left: 20px;
        }
    }

    &__title {
        font-family: $font-bold;
        line-height: 101px;
        font-size: 72px;
        letter-spacing: 0.015em;
        color: #FFFFFF;
        margin-top: 40px;

        @include tablet {
            font-size: 44px;
            line-height: 48px;
        }

        @include mobile {
            font-size: 32px;
            line-height: 45px;
        }
    }

    &__subtitle {
        font-family: $font-med;
        line-height: 51px;
        font-size: 36px;
        letter-spacing: 0.03em;
        color: #FFFFFF;
        margin-bottom: 30px;

        @include tablet {
            font-size: 28px;
            line-height: 34px;
        }

        @include mobile {
            font-size: 26px;
            margin-bottom: 32px;
        }
    }

    &__description {
        font-family: $gt-reg;
        line-height: 38px;
        font-size: 20px;
        letter-spacing: 0.03em;
        color: #FFFFFF;
        margin-bottom: 72px;

        @include tablet {
            font-size: 20px;
            line-height: 28px;
        }

        @include mobile {
            margin-bottom: 32px;
        }
    }

    &--text {
        padding-bottom: 1px;
        background: $dark;
    }

    &__bottom {
        padding-bottom: 31px;
        background: $dark;

        @include desktopLow {
            padding-bottom: 32px;
        }

        @include tabletLow {
            padding-bottom: 46px;
        }
    }

    &__header {
        display: flex;
        flex-wrap: wrap;
    }

    &__info {
        width: 80%;
    }

    &__social {
        width: 20%;
        padding-top: 4px;

        li {
            &:not(:first-child) {
                margin-top: 27px;
            }

            a {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
                border-radius: 50%;
                border: 1px solid rgba(#fff, .1);
                margin-left: auto;
            }
        }
    }

    &__figure {
        margin-top: 100px;
        margin-bottom: 90px;

        @include desktopLow {
            margin-top: 60px;
        }

        img {
            display: block;
            max-width: 100%;
            width: 100%;

            @media (min-width: 1340px) {
                max-width: 1340px;
            }
        }

        @include mobile {
            margin-top: 32px;
            margin-bottom: 60px;
            margin-right: -15px;
        }
    }

    &__details {
        margin-bottom: 130px;

        @include mobile {
            margin-bottom: 55px;
        }

        p {
            font-family: $font-light;
            line-height: 26px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: #FFFFFF;

            &:not(:first-child) {
                margin-top: 40px;
            }
        }

        a {
            font-family: $font-light;
            line-height: 26px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: #FFFFFF;
            border-bottom: 1px solid $accent;
            text-decoration: none;
            transition: all .4s;

            &:hover {
                border-bottom-color: transparent;
                color: $accent;
            }
        }
    }

    .contacts__bottom {
        margin-bottom: 100px;

        @include mobile {
            margin-bottom: 0px;
        }
    }
}
