.blockchain {

    &__intro {
        .back__link {
            left: 35px;

            @include tabletMedium {
                top: 10px;
            }

            @include tablet {
                top: 30px;
                left: 25px;
            }
        }

        .container {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            position: relative;
            padding-top: 4.5em;
            padding-bottom: 4.5em;

            @include tabletMedium {
                padding-top: 2em;
                padding-bottom: 2em;
                min-height: 680px;
            }

            @include tabletCustom {
                flex-direction: column;
                min-height: 1px;
                padding-top: 3em;
            }

        }

        &--text {
            width: calc(100% - 12% - 515px);
            margin-right: 9%;

            @include tabletMedium {
                max-width: 350px;
                width: 100%;
                margin-right: 0;
            }

            @include tabletCustom {
                max-width: 100%;
            }

            h1 {
                font-family: $font-bold;
                line-height: 79px;
                font-size: 56px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 13px;
                margin-top: 30px;

                @include tabletMedium {
                    line-height: 68px;
                    font-size: 48px;
                }

                @include mobile {
                    line-height: 45px;
                    font-size: 32px;
                    margin-bottom: 32px;
                }
            }

            p {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 50px;

                @include tabletMedium {
                    line-height: 28px;
                }

                @include tabletCustom {
                    margin-bottom: 0;
                }
            }

            .js-open-p {
                @include tabletCustom {
                    display: none;
                }
            }
        }

        &--image {
            width: 577px;
            min-height: 450px;
            position: relative;
            margin-right: 3%;

            .js-open-p {
                display: none;

                @include tabletCustom {
                    display: block;
                    margin: 30px auto 20px;
                }
            }

            @include tabletMedium {
                max-width: 417px;
                width: 100%;
                margin-right: 0;
                position: absolute;
                right: 30px;
                bottom: 20px;
            }

            @include tabletCustom {
                position: relative;
                right: 0;
                bottom: 0;
                margin: 50px auto 10px;
                min-height: 1px;
            }

            img {
                position: absolute;
                display: block;

                @include tabletCustom {
                    position: relative;
                }

                &:first-of-type {

                    left: 50%;
                    top: -40px;
                    transform: translate(-50%, 0%);
                    z-index: 5;

                    @include tabletMedium {
                        max-width: 95%;
                        top: 0;
                    }
                }

                &:last-of-type {

                    left: 50%;
                    bottom: 60px;
                    transform: translate(-50%, 0%);
                    z-index: 2;

                    @include tabletMedium {
                        max-width: 100%;
                        bottom: 30px;
                    }


                }
            }
        }

        button {
            width: 178px;
            display: block;
            height: 47px;
            background: $accent;
            border-radius: 100px;
            margin-right: 100px;
            text-decoration: none;
            font-size: 12px;
            font-family: $font-bold;
            line-height: 49px;
            text-align: center;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            color: #FFFFFF;
            transition: all 0.4s;
            position: relative;

            &:after {
                content: '';
                display: block;
                width: 152px;
                height: 41px;
                background: #000000;
                opacity: 0.2;
                filter: blur(18px);
                border-radius: 100px;
                position: absolute;
                left: 50%;
                bottom: -10px;
                transform: translate(-50%, 0%);
                z-index: -1;

            }

            @include mobile {
                width: 248px;
                margin: 0px auto;
            }

            &:hover {
                background: $hovered;
            }
        }
    }
}


.tech {
    &__work {
        background-color: $some;
        padding: 6.25em 0em;

        @include tabletMedium {
            padding: 5em 0em;
        }

        @include tabletLow {
            padding: 4em 0em;
        }

        .container {
            position: relative;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;

            @include tabletMedium {
                flex-direction: column;
            }
        }

        &--left {
            max-width: 520px;
            width: 100%;
            margin-right: 12%;

            @include tabletMedium {
                margin-right: 0;
            }

            h2,
            h3,
            h4,
            h5,
            h6 {
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                letter-spacing: 0.03em;
                color: $white;

                @include tabletLow {
                    line-height: 33px;
                    font-size: 20px;
                }
            }
        }

        &--inner {
            @include tabletMedium {
                width: 100%;
                max-width: 520px;
            }

            @include tabletLow {
                max-width: 100%;
            }

        }

        &--right {
            width: calc(100% - 12% - 520px);


            @include tabletMedium {
                width: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: flex-end;
                margin-top: 60px;
            }


            h2,
            h3,
            h4,
            h5,
            h6 {
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                letter-spacing: 0.03em;
                color: $white;
                margin-bottom: 21px;

                @include tabletLow {
                    line-height: 33px;
                    font-size: 20px;
                    margin-bottom: 32px;
                }
            }

            ul {
                list-style-type: none;
                list-style-image: none;

                li {
                    padding-left: 34px;
                    margin-bottom: 20px;
                    font-family: $gt-reg;
                    font-weight: 500;
                    line-height: 30px;
                    font-size: 16px;
                    letter-spacing: 0.03em;
                    color: $white;
                    position: relative;

                    @include tabletLow {
                        line-height: 28px;
                        padding-left: 20px;
                    }

                    &:before {
                        content: '';
                        display: block;
                        width: 4px;
                        height: 4px;
                        position: absolute;
                        left: 0;
                        top: 13px;
                        background-color: $accent;
                        border-radius: 50%;

                        @include tabletLow {
                            top: 12px;
                        }
                    }

                    p,
                    span,
                    b {
                        font-size: inherit;
                        line-height: inherit;
                        color: inherit;
                    }
                }
            }
        }

        &--bg {
            position: absolute;
            right: 0;
            top: -120px;
            width: 748px;
            height: 252px;
            background: url('../img/technologies/work-bg.png');
            background-size: cover;
            background-repeat: no-repeat;
            z-index: 0;

            @include tabletMedium {
                right: -35%;
            }
        }
    }


    &__what {
        padding: 8em 0em 3.5em;
        background-color: #F5F5F5;

        @include tabletMedium {
            padding: 5em 0em 3.5em;
        }

        @include tabletLow {
            padding: 3.75em 0em 1.5em;
        }

        h3 {
            max-width: 520px;
            width: 100%;
            margin-right: 12%;
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: $dark;

            &.subtitile {
                margin-bottom: 97px;
                margin-top: 40px;
                max-width: 100%;
                margin-right: 0;

                @include tabletMedium {
                    margin-bottom: 80px;
                }

                @include tabletCustom {
                    margin-bottom: 38px;
                }
            }

            @include tabletMedium {
                margin-bottom: 40px;
            }

            @include tabletLow {
                line-height: 37px;
                font-size: 26px;
                margin-bottom: 32px;
            }

        }

        &--top {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-wrap: wrap;

            @include tabletMedium {
                flex-direction: column;

            }



            ul {
                list-style-image: none;
                list-style-type: none;

                @include tabletMedium {
                    max-width: 500px;
                    width: 100%;
                }

                li {
                    padding-left: 34px;
                    margin-bottom: 20px;
                    font-family: $gt-reg;
                    font-weight: 500;
                    line-height: 30px;
                    font-size: 16px;
                    letter-spacing: 0.03em;
                    color: $dark;
                    position: relative;

                    @include tabletLow {
                        line-height: 28px;
                    }

                    &:before {
                        content: '';
                        display: block;
                        width: 4px;
                        height: 4px;
                        position: absolute;
                        left: 0;
                        top: 13px;
                        background-color: $accent;
                        border-radius: 50%;
                    }

                    p,
                    span,
                    b {
                        font-size: inherit;
                        line-height: inherit;
                        color: inherit;
                    }
                }
            }
        }

        &--inner {
            width: calc(100% - 12% - 520px);

            @include tabletMedium {
                width: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: flex-end;
            }
        }

        &--list {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-wrap: wrap;
        }

        &--item {
            width: calc(100%/3 - 40px);
            margin-right: 60px;
            min-height: 279px;
            background: $white;
            border-radius: 5px;
            padding: 40px 30px 25px;
            position: relative;
            margin-bottom: 40px;

            @include desktopLow {
                width: calc(100%/2 - 30px);
                margin-bottom: 70px;
            }

            @include tabletMedium {
                width: calc(100%/2 - 12px);
                padding: 40px 24px 30px;
            }

            @include tabletCustom {
                max-width: 500px;
                width: 100%;
                padding: 25px 18px;
                margin-bottom: 32px;
            }

            h4 {
                font-family: $font-med;
                font-weight: 600;
                font-size: 30px;
                line-height: 32px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 20px;

                @include tablet {
                    line-height: 33px;
                    font-size: 20px;
                    margin-bottom: 16px;
                }
            }

            p,
            span {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;
            }



            &:last-of-type {
                margin-right: 0;
            }

            &:nth-of-type(odd) {
                @include desktopLow {
                    margin-right: 60px;
                }

                @include tabletMedium {
                    margin-right: 24px;
                }

                @include tabletCustom {
                    margin: 0px auto 32px;
                }
            }

            &:nth-of-type(even) {
                @include desktopLow {
                    margin-right: 0px;
                }

                @include tabletMedium {
                    margin-right: 0;
                }

                @include tabletCustom {
                    margin: 0px auto 32px;
                }
            }
        }

        &--image {
            width: 90px;
            height: 90px;
            background: $white;
            box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
            border-radius: 3px;
            position: absolute;
            right: 27px;
            top: -30px;

            @include tabletMedium {
                right: 20px;
            }

            @include tabletCustom {
                position: relative;
                right: 0;
                top: 0;
                width: 55px;
                height: 55px;
                margin-bottom: 15px;
            }

            img {
                display: block;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);

                @include tabletCustom {
                    max-width: 80%;
                }
            }
        }
    }

    &__description {

        @include tabletCustom {
            display: none;
        }

        .container {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-wrap: wrap;

        }

        &--right {
            background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
        }

        &--left {

            background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
        }

        &--wrapp {
            width: 50%;
            padding: 40px;
            height: 347px;

            @include tabletMedium {
                height: 400px;
            }

            h3,
            h4,
            h5,
            h6 {
                font-family: $gt-reg;
                font-weight: 500;
                line-height: 35px;
                font-size: 20px;
                letter-spacing: 0.03em;
                color: $accent;
                margin-bottom: 40px;
            }

            ul {
                list-style-type: none;
                list-style-image: none;

                li {
                    font-family: $gt-reg;
                    font-weight: 500;
                    line-height: 30px;
                    font-size: 16px;
                    letter-spacing: 0.03em;
                    color: $dark;
                    position: relative;
                    padding-left: 34px;

                    @include tabletLow {
                        line-height: 28px;
                    }

                    &:before {
                        content: '';
                        display: block;
                        width: 4px;
                        height: 4px;
                        position: absolute;
                        left: 0;
                        top: 13px;
                        background-color: $accent;
                        border-radius: 50%;
                    }
                }
            }
        }
    }


}
