.member {
    height: calc(100vh - 220px);

    @include desktop {
        height: auto;
        overflow: hidden;
    }

    @include desktopLow {
        min-height: 90vh;
    }

    @include mix {
        height: auto;
        overflow: hidden;
    }

    @include mixLarge {
        height: 130vh;
    }

    .container {
        position: relative;
        height: 100%;

        @include tabletLow {
            display: flex;
            flex-direction: column-reverse;
            height: auto;
        }
    }

    .back__link {
        left: 35px;

        @include desktopLow {
            top: 46px;
        }

        @include tablet {
            top: 35px;
        }

        @include tabletLow {
            left: 20px;
        }
    }

    &__bg {
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
    }

    &__main {
        display: flex;
        width: calc(100% - 400px);
        height: 100%;
        justify-content: flex-start;
        align-items: flex-end;
        position: absolute;
        left: 0;
        top: 0;

        @include desktop {
            flex-direction: column;
            position: relative;
            align-items: flex-start;
        }

        @include tablet {
            width: calc(100% - 300px);
        }

        @include tabletLow {
            flex-direction: column-reverse;
            width: 100%;
            justify-content: center;
            align-items: center;
            position: relative;
        }

    }

    &__tabs {
        max-width: 400px;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        padding-right: 4em;
        padding-top: 5em;
        z-index: 6;
        position: absolute;
        right: 0;
        top: 0;

        .slimScrollDiv {
            height: auto !important;
        }

        @include tablet {
            padding: 3em 0em;
            max-width: 300px;
        }

        @include tabletLow {
            position: relative;
            max-width: 100%;
            margin-bottom: 15px;
            padding-bottom: 0;
            padding-top: 5.5em;
        }

        &--item {
            width: 100%;
            overflow: hidden;

            @include tabletLow {
                overflow: initial;
            }
        }

        &--category {
            font-family: $font-bold;
            line-height: 49px;
            font-size: 30px;
            letter-spacing: 0.03em;
            color: $udark;
            margin-bottom: 30px;
            cursor: pointer;
            transition: all 0.4s;



            @include tabletLow {
                font-size: 20px;
                line-height: 33px;
                width: 100%;
                position: relative;
            }

            &:after {
                content: '';
                display: inline-block;
                vertical-align: middle;
                position: relative;
                margin-left: 10px;
                width: 0;
                height: 0;
                border: 0 solid transparent;
                border-left-width: 4px;
                border-right-width: 4px;
                border-top: 7px solid $udark;
                top: -2px;
                transition: all 0.4s;

                @include tabletLow {
                    position: absolute;
                    display: block;
                    margin: 0;
                    right: 0;
                    top: 10px;


                }
            }

            &:hover {
                color: $accent;

                &:after {
                    border-top-color: $accent;
                }
            }

            &.active {
                color: $accent;

                &:after {
                    border-top-color: $accent;
                    transform: rotate(-180deg);
                }
            }

        }

        &--list {

            li {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: rgba(0, 0, 0, 0.7);
                transition: color 0.4s ease-in;
                margin-bottom: 27px;
                cursor: pointer;

                &:before {
                    content: '';
                    display: none;
                    width: calc(100% + 30px);
                    height: calc(100% + 15px);
                    background: #F5F5F5;
                    position: absolute;
                    left: 50%;
                    top: -15px;
                    transform: translate(-50%, 0%);
                    opacity: 0;
                    transition: all 0.35s;
                    z-index: -1;
                }

                img {
                    display: none;
                    margin: 10px auto 0px;
                    max-width: 300px;
                    height: auto;
                }

                span {
                    display: none;
                    font-family: $gt-reg;
                    line-height: 28px;
                    font-size: 16px;
                    letter-spacing: 0.03em;
                    color: #181818;
                }

                @include tabletLow {
                    font-family: $font-bold;
                    line-height: 30px;
                    font-size: 18px;
                    position: relative;

                    span {
                        display: block;

                    }
                }

                &:hover {
                    color: $accent;
                }

                &.active-link {
                    color: $accent;

                    &:before {
                        @include tabletLow {
                            display: block;
                            opacity: 1;
                        }
                    }

                    img {
                        @include tabletLow {
                            display: block;
                        }
                    }

                }
            }
        }

    }


    &__text {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        max-width: 300px;
        width: 100%;
        height: 100%;
        position: relative;

        @include desktop {
            min-height: 250px;
            margin-top: 100px;
            max-width: 100%;
        }

        @include tabletMedium {
            min-height: 320px;
        }

        @include tablet {
            min-height: 215px;
            margin-bottom: 100px;
            align-items: flex-start;

        }

        @include tabletLow {
            max-width: 100%;
            margin-top: 30px;
            margin-bottom: 45px;
            min-height: 65px;
            display: none;
        }

        ul {
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            padding-right: 30px;
            z-index: 6;

            @include tabletMedium {
                height: auto;
                top: 135px;
            }

            @include tabletLow {
                top: 30px;
            }

            li {
                // display: none;
                position: absolute;
                top: 50%;
                left: 30px;
                transform: translate(0%, -50%);
                opacity: 0;

                @include tablet {
                    left: 0;
                }

                &:after {
                    content: '';
                    display: block;
                    width: 0%;
                    height: 100%;
                    position: absolute;
                    background: $accent;
                    left: 0;
                    top: 0;

                }
            }

        }
    }

    &--name {
        font-family: $font-bold;
        line-height: 56px;
        font-size: 40px;
        letter-spacing: 0.03em;
        color: $udark;
        margin-bottom: 30px;

        @include tabletLow {
            font-size: 18px;
            line-height: 30px;
            color: rgba(0, 0, 0, 0.7);
            margin-bottom: 5px;
        }
    }

    &--job {
        display: block;
        font-family: $gt-reg;
        line-height: 38px;
        font-size: 20px;
        letter-spacing: 0.03em;
        color: $udark;

        @include tabletLow {
            font-size: 16px;
            line-height: 28px;
        }
    }

    &__image {
        width: calc(100% - 300px);
        height: 100%;
        position: relative;

        @include desktop {
            min-height: 588px;
            height: auto;
            width: 100%;
        }



        @include tabletMedium {
            min-height: 425px;
        }

        @include tabletLow {
            min-height: 415px;
            display: none;
        }

        // overflow: hidden;

        &--inner {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0;
        }

        &--mask {
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            background: $white;
            left: 0;
            bottom: 0;
            z-index: 4;
            transform: translate3d(-100%, 0, 0);
            pointer-events: none;
        }


        img {
            max-width: calc(100% - 20px);
            max-height: 600px;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translate(-50%, 0%);
            z-index: 2;

            @include tabletMedium {
                max-height: 570px;
            }

            @include tabletLow {
                max-height: 100%;
            }

        }
    }
}
