.main {

    // intro sections styles
    .intro {
        position: relative;
        margin-bottom: 200px;

        @include desktopLow {
            margin-bottom: 30px;
        }

        .container {
            position: relative;

            @include desktopLow {
                z-index: 10;
            }
        }

        &__text {
            padding-top: 4em;
            padding-right: 6em;
            display: flex;
            justify-content: flex-end;
            position: relative;
            z-index: 4;

            @include desktopLow {
                padding-right: 0em;
            }

            @include tablet {
                justify-content: center;
                padding-top: 1.5em;
                margin-bottom: 90px;
            }

            @include mobile {
                padding-top: 2em;
            }

            p {
                font-family: $gt-reg;
                line-height: 28px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: rgba(24, 24, 24, 0.8);
                margin-bottom: 100px;
                max-width: 520px;
                width: 100%;

                @include desktopLow {
                    margin-bottom: 50px;
                }

                @include tablet {
                    max-width: 100%;
                }
            }
        }

        &__main {
            @include desktopLow {
                position: relative;
                z-index: 5;
            }

            h1 {
                font-family: $font-bold;
                line-height: 79px;
                font-size: 56px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 40px;

                @include tablet {
                    font-size: 40px;
                    line-height: 44px;
                }

                @include mobile {
                    font-size: 32px;
                    line-height: 45px;
                }
            }

            &--btns {
                display: flex;
                justify-content: flex-start;
                align-items: center;

                @include mobile {
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                }

                a {
                    display: inline-flex;
                    width: 178px;
                    height: 47px;
                    margin-right: 22px;
                    background: $dark;
                    border-radius: 100px;
                    justify-content: center;
                    align-items: center;
                    font-family: $font-bold;
                    font-size: 12px;
                    text-align: center;
                    letter-spacing: 0.1em;
                    text-transform: uppercase;
                    color: $white;
                    text-decoration: none;
                    transition: all 0.4s;

                    @include mobile {
                        width: 248px;
                        margin-bottom: 16px;
                    }

                    &:hover,
                    &:focus {
                        background: $accent;
                    }

                    &:before {
                        margin-right: 8px;
                        font-size: 18px;
                    }
                }
            }
        }

        &__bg {
            position: absolute;
            width: 892px;
            height: 892px;
            left: 3.5%;
            top: -37%;
            transform: translate(-50%, 0%);
            z-index: -1;
            background: url('../img/main/circles.svg');
            background-repeat: no-repeat;
            background-size: contain;


            @include tabletMedium {
                left: 5%;
                width: 816px;
                height: 816px;
            }

            @include tablet {
                width: 434px;
                height: 434px;
                top: 5%;
            }

            @include mobile {
                top: 15%;
            }

            @include mobile-small {
                top: 20%;
                left: 6%;
            }
        }
    }

    // helpful sections styles
    .helpful {
        padding-top: 100px;
        padding-bottom: 254px;
        background-color: $white;
        position: relative;

        @include tabletMedium {
            padding-bottom: 3em;
            padding-top: 3em;
        }

        &__line {
            opacity: 0;
            position: absolute;
            z-index: 1;
            height: 3px;
            width: 0;
            background-color: $accent;
            top: 490px;
            left: 50%;
            transform: translate(-50%, 0%);
            transition: opacity 0.6s, width 0.6s;

            @include tabletMedium {
                display: none;
            }

            &.is-active {
                width: 100%;
                opacity: 1;
                right: 0;
            }
        }

        svg {
            position: absolute;
            width: 100%;
            height: 300px;
            bottom: -28px;
            left: 0;
            z-index: 100;
        }

        .traceability-svgs {
            height: 600px;
            z-index: 20;
            top: 47.5%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .road-image {
            position: relative;
            width: 100%;
            min-height: 516px;

            img {
                position: absolute;
                display: block;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: 4;
            }

            &:before {
                content: '';
                display: block;
                width: 370px;
                height: 370px;
                border-radius: 50%;
                background: #FFFFFF;
                opacity: 0.5;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: 7;
            }
        }

        #main-road {
            position: absolute;
            width: 530px;
            height: 530px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        #wave-one {
            fill: none;
            stroke-width: 1;
            stroke-linecap: round;
            // stroke-linejoin: round;
            stroke-dasharray: 1, 10;
            stroke: $dark;
        }

        #wave-two {
            fill: none;
            stroke-width: 1;
            stroke-linecap: round;
            // stroke-linejoin: round;
            stroke-dasharray: 1, 10;
            stroke: $accent;
        }

        &__inner {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
        }

        &__text {
            max-width: 520px;
            width: 100%;
            margin-right: 35px;
            padding-top: 4em;

            @include tabletMedium {
                max-width: 100%;
                padding-top: 0;
                padding-bottom: 3em;
            }

            h2 {
                font-family: $font-bold;
                line-height: 56px;
                font-size: 40px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 20px;

                @include mobile {
                    font-size: 26px;
                    line-height: 37px;
                    margin-bottom: 32px;
                }
            }

            p {
                font-family: $gt-reg;
                line-height: 28px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: rgba(24, 24, 24, 0.8);
                margin-bottom: 20px;
            }

            p:last-of-type {
                margin-bottom: 0px;
            }
        }

        &__image {
            position: relative;
            min-height: 516px;
            width: calc(100% - 555px);
            overflow: hidden;
            position: relative;

            @include tabletMedium {
                display: none;
            }

            canvas#musicWave {
                display: block;
                position: absolute;
                width: 100%;
                left: 0;
                bottom: 125px;
                z-index: 10;
            }

            canvas#lineWaves {
                display: block;
                position: absolute;
                left: 0;
                top: 76%;
                transform: translate(0%, -50%);
                z-index: 12;
            }

            img {
                position: absolute;
                display: block;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: 15;
                max-height: 516px;

                @include desktopLow {
                    max-width: 100%;
                }
            }
        }

        &__tabs {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
            position: relative;
            margin-top: 130px;

            @include tabletMedium {
                margin-top: 60px;
            }

            &--item {
                width: calc(100%/4 - 30px);
                font-family: $gt-reg;
                line-height: 40px;
                font-size: 23px;
                letter-spacing: 0.03em;
                color: rgba(24, 24, 24, 0.4);
                position: relative;
                text-align: left;
                margin-right: 60px;
                padding-top: 19px;
                padding-bottom: 5px;
                background: transparent;
                transition: all 0.4s;
                cursor: pointer;

                @include tabletMedium {
                    margin-right: 20px;
                    width: calc(100%/4 - 10px);
                    font-size: 18px;
                }

                @include mobile {
                    margin-right: 16px;
                }

                span {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    width: 100%;
                    background: rgba(24, 24, 24, 0.1);
                    height: 3px;
                    border-radius: 50px;

                    &:before {
                        content: '';
                        display: block;
                        height: 100%;
                        width: 0;
                        position: absolute;
                        top: 0;
                        left: 0;
                        background-color: $dark;

                    }
                }

                &.tab--active {
                    color: $dark;

                    span:before {
                        animation: full 10.3s linear 1;
                    }


                }
            }

            &--item:last-of-type {
                margin-right: 0;
            }
        }

        &__btns {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            position: relative;
            z-index: 20;
            margin-top: -30px;

            @include tabletMedium {
                margin-top: 0px;
            }

            @include mobile {
                justify-content: center;
                align-content: center;

                .main-controls {
                    display: none;
                }
            }

            a {
                width: 178px;
                display: block;
                height: 47px;
                background: $accent;
                border-radius: 100px;
                margin-right: 100px;
                text-decoration: none;
                font-size: 12px;
                font-family: $font-bold;
                line-height: 49px;
                text-align: center;
                letter-spacing: 0.1em;
                text-transform: uppercase;
                color: #FFFFFF;
                transition: all 0.4s;
                position: relative;

                &.ie-btn {
                    &:after {
                        display: none;
                    }
                }

                &:after {
                    content: '';
                    display: block;
                    width: 152px;
                    height: 41px;
                    background: #000000;
                    opacity: 0.2;
                    filter: blur(18px);
                    border-radius: 100px;
                    position: absolute;
                    left: 50%;
                    bottom: -10px;
                    transform: translate(-50%, 0%);
                    z-index: -1;

                }

                @include mobile {
                    width: 248px;
                    margin: 0px auto;
                }

                &:hover {
                    background: $hovered;
                }
            }
        }
    }


    // styles for work schema section
    .work-scheme {
        padding: 9.4em 0em 12.5em;
        background-color: $dark;

        h2 {
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: #FFFFFF;
            margin-bottom: 48px;

            @include tablet {
                font-size: 30px;
                line-height: 34px;
            }
        }

        @include desktop {
            overflow: hidden;
            padding: 7em 0em 5em;
        }

        @include mobile {
            padding: 60px 0px;
        }

        &__icons {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            margin-bottom: 228px;

            @include desktop {
                margin-bottom: 70px;
            }

            @include tablet {
                flex-direction: column;
                justify-content: center;
                align-content: center;
            }

            @include mobile {
                margin-bottom: 60px;
            }
        }

        &__item {
            width: calc(100%/3 - 21px);
            margin-right: 42px;

            @include tablet {
                width: 100%;
                margin: 0px auto 40px;
            }

            img {
                display: block;
                margin-bottom: 27px;

                @include tablet {
                    margin-bottom: 30px;
                }
            }

            p {
                font-family: $gt-reg;
                line-height: 28px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: #FFFFFF;
            }
        }

        &__item:last-of-type {
            margin-right: 0;

            @include tablet {
                margin: 0px auto 40px;
            }

            img {
                margin-bottom: 19px;

                @include tablet {
                    margin-bottom: 30px;
                }
            }
        }

        &__app {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            @include desktop {
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }

            &--text {
                max-width: 725px;
                width: 100%;

                @include desktop {
                    max-width: 100%;
                }

                h3,
                h4,
                h5,
                h6 {
                    max-width: 620px;
                    width: 100%;
                    font-family: $font-bold;
                    line-height: 49px;
                    font-size: 30px;
                    letter-spacing: 0.03em;
                    color: #FFFFFF;
                    margin-bottom: 40px;

                    @include desktop {
                        max-width: 100%;
                    }

                    @include mobile {
                        font-size: 20px;
                        line-height: 33px;
                    }
                }

                p {
                    font-family: $gt-reg;
                    font-size: 20px;
                    line-height: 24px;
                    letter-spacing: 0.03em;
                    color: rgba(255, 255, 255, 0.5);
                    transition: all 0.4s;
                }
            }

            &--items {
                margin-bottom: 74px;
                padding-right: 1.5em;

                @include tablet {
                    padding: 0em;
                    margin-bottom: 45px;
                }

            }

            &--item {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                cursor: pointer;
                margin-bottom: 26px;
                // pointer-events: none;

                &.is-active {
                    p {
                        color: $white;
                    }

                    .svg2 {
                        opacity: 1;
                        animation: dash 10s linear alternate 1;
                    }

                    .ie-num .svg2 {
                        display: none;
                    }


                }

                &:hover {
                    p {
                        color: $white;
                    }
                }
            }

            &--number {
                width: 50px;
                height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                margin-right: 54px;

                @include mobile {
                    margin-right: 16px;
                }

                span {
                    font-family: $font-reg;
                    line-height: 38px;
                    font-size: 16px;
                    text-align: center;
                    letter-spacing: 0.03em;
                    color: $white;
                }

                svg {
                    position: absolute;
                    top: 57%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    pointer-events: none;
                }

                .svg1 {
                    stroke-dasharray: 806;
                    position: relative;
                    z-index: 2;
                    opacity: 0.1;
                }

                .svg2 {
                    opacity: 0;
                    stroke-dasharray: 806;
                    stroke-dashoffset: 800;
                    position: relative;
                    z-index: 4;
                }


            }

            &--name {
                width: calc(100% - 104px);
                display: flex;
                align-items: center;
            }

            &--btns {
                display: flex;
                justify-content: flex-start;
                align-items: center;

                @include mobile {
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                }

                a {
                    display: inline-flex;
                    justify-content: center;
                    align-items: center;
                    width: 178px;
                    height: 47px;
                    font-family: $font-bold;
                    font-size: 12px;
                    text-align: center;
                    letter-spacing: 0.1em;
                    text-transform: uppercase;
                    text-decoration: none;
                    color: $dark;
                    background: $white;
                    border-radius: 100px;
                    transition: all 0.4s;

                    @include mobile {
                        width: 248px;
                        margin: 0px auto 16px;
                    }

                    &:hover,
                    &:focus {
                        background: $accent;
                        color: $white;
                    }

                    &:before {
                        margin-right: 8px;
                        font-size: 18px;
                    }
                }

                a:first-of-type {
                    margin-right: 22px;

                    @include mobile {
                        margin: 0px auto 16px;
                    }
                }
            }

            &--image {
                position: relative;
                width: calc(100% - 725px);
                min-height: 620px;
                z-index: 2;

                @include tablet {
                    display: none;
                }

                &__inner {
                    display: none;
                    width: 215px;
                    height: 457px;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 5;

                    &:after {
                        content: '';
                        display: block;
                        width: 92px;
                        height: 4px;
                        border-radius: 5px;
                        position: absolute;
                        left: 50%;
                        bottom: 14px;
                        transform: translate(-50%, 0%);
                        z-index: 7;
                    }

                    h4 {
                        font-family: $font-bold;
                        line-height: 22px;
                        font-size: 18px;
                        text-align: center;
                        letter-spacing: 0.03em;
                        margin-bottom: 3px;
                        color: $dark;
                    }

                    p {
                        font-family: $font-med;
                        line-height: 13px;
                        font-size: 8px;
                        text-align: center;
                        letter-spacing: 0.03em;
                        text-transform: uppercase;
                        color: rgba(24, 24, 24, 0.5);
                    }


                }

                &__inner:nth-of-type(1) {
                    padding-top: 69px;

                    &:after {
                        background: rgba(255, 255, 255, 0.2);
                    }

                    img {
                        display: block;
                        margin: 0px auto 11px;
                        position: relative;
                        left: 10px;
                        max-width: 100%;
                        z-index: 3;
                        opacity: 0;
                        transform: translateY(40px);
                    }

                    span.shadow {
                        display: block;
                        width: 127px;
                        height: 17px;
                        position: absolute;
                        top: 237px;
                        left: 57px;
                        background: linear-gradient(273.93deg, #EDDEDE -19.97%, rgba(237, 222, 222, 0) 79.25%);
                        filter: blur(6px);
                        z-index: 1;

                        &.ie-shadow {
                            display: none;
                        }
                    }

                    h4,
                    p {
                        opacity: 0;
                        transform: translateY(40px)
                    }



                    i {
                        display: block;
                        width: 44px;
                        height: 44px;
                        margin: 24px auto 0px;
                        border-radius: 50%;
                        background: $white;
                        box-shadow: 0px 4px 25px rgba(197, 117, 117, 0.27);
                        position: relative;
                        z-index: 4;
                        left: -1px;

                        &:before {
                            content: '';
                            display: block;
                            width: 22px;
                            height: 22px;
                            background: url('../img/main/wifi.svg');
                            background-size: cover;
                            background-repeat: no-repeat;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);

                        }
                    }

                    div {
                        width: 100%;
                        height: 104px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex-direction: column;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        background: url('../img/main/subtract.svg');
                        background-repeat: no-repeat;
                        background-size: cover;
                        z-index: 2;

                        span {
                            font-family: $font-med;
                            line-height: 16px;
                            font-size: 10px;
                            text-align: center;
                            letter-spacing: 0.03em;
                            color: $white;
                        }

                        span:first-of-type {
                            margin-top: 6px;
                            margin-bottom: 4px;
                        }
                    }

                    &.active-tab {
                        img {
                            animation: fadeIn 0.3s linear forwards;
                            animation-delay: 0.4s;
                        }

                        h4,
                        p {
                            animation: fadeIn 0.3s linear forwards;
                            animation-delay: 0.75s;
                        }


                    }
                }

                &__inner:nth-of-type(2) {
                    padding-top: 47px;

                    &:after {
                        background: rgba(197, 117, 117, 0.1);
                    }

                    img {
                        display: block;
                        margin: 0px auto;
                        opacity: 0;
                    }

                    &.active-tab {
                        img {
                            animation: fadeIn 0.3s linear forwards;
                            animation-delay: 0.4s;
                        }
                    }
                }

                &__inner:nth-of-type(3) {
                    padding-top: 69px;

                    &:before {
                        content: '';
                        display: block;
                        width: 144px;
                        height: 144px;
                        border-radius: 50%;
                        background-color: $accent;
                        position: absolute;
                        top: 85px;
                        left: 50%;
                        transform: translate(-50%, 0%) scale(0);
                        z-index: 2;
                    }

                    &:after {
                        background: rgba(197, 117, 117, 0.1);
                    }

                    img {
                        display: block;
                        margin: 0px auto 11px;
                        position: relative;
                        left: 10px;
                        max-width: 100%;
                        z-index: 4;
                        margin-bottom: 22px;
                        opacity: 0;
                    }

                    h4,
                    p {
                        opacity: 0;
                        transform: translate(40px);
                    }

                    span.shadow {
                        display: block;
                        width: 140px;
                        height: 23px;
                        position: relative;
                        margin-left: 57px;
                        margin-bottom: 11px;
                        background: #F9EFEF;
                        opacity: 0.5;
                        filter: blur(6px);

                        &.ie-shadow {
                            display: none;

                            &:before {
                                display: none;
                            }

                        }

                        &:before {
                            content: '';
                            display: block;
                            width: 127px;
                            height: 17px;
                            position: absolute;
                            right: 0;
                            top: 50%;
                            transform: translate(0%, -50%);
                            background: linear-gradient(273.93deg, #EDDEDE -19.97%, rgba(237, 222, 222, 0) 79.25%);
                            filter: blur(6px);
                        }
                    }

                    a {
                        display: flex;
                        width: 178px;
                        height: 38px;
                        justify-content: center;
                        align-items: center;
                        margin: 24px auto 0px;
                        background: $accent;
                        border-radius: 100px;
                        text-decoration: none;
                        font-size: 12px;
                        font-family: $font-bold;
                        text-align: center;
                        letter-spacing: 0.1em;
                        text-transform: uppercase;
                        color: $white;
                        transition: all 0.4s;

                        &:hover,
                        &:focus {
                            background: $hovered;
                        }

                        &:before {
                            content: '';
                            display: block;
                            width: 14px;
                            height: 14px;
                            margin-right: 8px;
                            position: relative;
                            background: url('../img/icons/buy.svg');
                            background-size: cover;
                            background-repeat: no-repeat;
                        }
                    }


                    &.active-tab {
                        img {

                            animation: fade 0.35s linear forwards;
                            animation-delay: 1s;
                        }

                        &:before {
                            animation: scaleUp 0.35s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
                            animation-delay: 0.4s;
                        }

                        h4,
                        p {
                            animation: fadeIn 0.35s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
                            animation-delay: 1s;
                        }


                    }


                }
            }

            &--mobile {
                position: absolute;
                display: block;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }

            &--bg {
                width: 620px;
                height: 620px;
                position: absolute;
                top: 0;
                left: -55px;
                z-index: 0;
                background: url('../img/main/circle-small.svg');
                background-repeat: no-repeat;
                background-size: contain;

                @include desktop {
                    left: 50%;
                    transform: translate(-50%, 0%)
                }

                // &__item {
                //     position: absolute;
                //     top: 50%;
                //     left: 50%;
                //     transform: translate(-50%, -50%);
                //     border-radius: 50%;
                //     border: 2px dotted $accent;
                // }

                // &__item:nth-of-type(1) {
                //     width: 100%;
                //     height: 100%;
                //     opacity: 0.3;
                // }

