                // }

            }
        }

    }

    // styles for tagging section
    .tagging {
        padding: 9.4em 0em 7.5em;
        background-color: #F5F5F5;

        @include tabletMedium {
            padding: 4em 0em;
        }

        @include mobile {
            padding: 60px 0px 30px;
        }

        h2 {
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 60px;

            @include tablet {
                font-size: 30px;
                line-height: 34px;
            }

            @include mobile {
                font-size: 26px;
                line-height: 30px;
            }
        }

        .tagging__items {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;

            @include tabletMedium {
                justify-content: center;
                align-items: center;
                flex-direction: column;
            }

            &--item {
                min-height: 230px;
                width: calc(100%/2 - 30px);
                margin-right: 60px;
                background: #FFFFFF;
                border-radius: 3px;
                position: relative;
                padding: 1.9em 2em 1.9em 2.5em;
                margin-bottom: 30px;
                transition: all 0.4s;
                cursor: pointer;

                @include tabletMedium {
                    max-width: 400px;
                    width: 100%;
                    margin: 0px auto 50px;
                }

                h3 {
                    padding-left: 20px;
                    font-family: $font-med;
                    font-size: 30px;
                    line-height: 34px;
                    letter-spacing: 0.03em;
                    color: #181818;
                    margin-bottom: 19px;
                    transition: all 0.4s;

                    @include mobile {
                        font-size: 22px;
                        line-height: 26px;
                    }
                }

                ul {
                    list-style: none;
                    list-style-image: none;
                }

                li {
                    font-family: $gt-reg;
                    line-height: 20px;
                    font-size: 16px;
                    letter-spacing: 0.03em;
                    color: $dark;
                    margin-bottom: 20px;
                    padding-left: 20px;
                    position: relative;
                    transition: all 0.4s;

                    &:before {
                        content: '';
                        display: block;
                        width: 4px;
                        height: 4px;
                        border-radius: 50%;
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translate(0%, -50%);
                        background-color: $dark;
                        transition: inherit;
                    }
                }

                li:last-of-type {
                    margin-bottom: 0px;
                }

                &:hover {
                    background: $accent;
                    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
                }

                &:hover h3 {
                    color: $white;
                }

                &:hover li {
                    color: $white;

                    &:before {
                        background-color: $white;
                    }
                }
            }

            &--item:last-of-type {
                margin-right: 0px;

                @include tabletMedium {
                    margin: 0px auto 30px;
                }

            }

            &--icon {
                position: absolute;
                right: 10px;
                top: -50px;
                z-index: 4;
                opacity: 0;
                transition: all 0.4s;

                @include mobile {
                    opacity: 1;
                    right: 80px;
                    width: 64px;
                    height: 64px;
                }

                img {
                    display: block;
                }
            }

            &--item:hover .tagging__items--icon {
                opacity: 1;
            }


        }
    }


}

// styles for partners section
.partners {
    position: relative;
    background-color: #F5F5F5;

    &:before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 113px;
        background-color: $dark;
        bottom: 0;
        left: 0;
        z-index: 0;
    }



    &__inner {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        @include tabletMedium {
            flex-direction: column;
            justify-content: center;
        }

    }

    &__text {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        max-width: 412px;
        width: 100%;
        min-height: 419px;
        padding: 2em 2em 2em 0em;
        position: relative;
        z-index: 5;

        @include tabletMedium {
            min-height: 100px;
            max-width: 100%;
            padding: 1em 0em;
            z-index: 10;
        }

        h2 {
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: $dark;

            @include tablet {
                font-size: 30px;
                line-height: 34px;
            }

            @include mobile {
                font-size: 26px;
                line-height: 30px;
            }
        }


    }

    &__slider {
        min-height: 419px;
        padding: 4.4em 6em 6em;
        width: calc(100% - 415px);
        background: $white;
        box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05);
        border-radius: 3px;
        position: relative;
        z-index: 5;



        @include desktop {
            padding: 3em 3em;
        }

        @include tabletMedium {
            width: 100%;
            padding: 6em 3em;
        }

        @include mobile {
            padding: 77px 16px 55px;
        }

        img {
            display: block;
            max-height: 50px;
            margin-bottom: 40px;
        }

        p {
            font-family: $gt-reg;
            line-height: 28px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: rgba(24, 24, 24, 0.8);
        }



        &--status {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            &.ie-status {
                position: absolute;
                bottom: 0;
                left: 0;

                @include tabletMedium {
                    position: absolute;
                    bottom: -60px;
                    left: 30px;
                }

                @include mobile {
                    left: 16px;
                }
            }

            @include tabletMedium {
                position: absolute;
                bottom: -60px;
                left: 30px;
            }

            @include mobile {
                left: 16px;
            }

            span {
                font-family: $font-med;
                line-height: 38px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: #F5F5F5;

                @include tabletMedium {
                    color: $dark;
                }
            }

            .status-line {
                display: block;
                width: 194px;
                height: 2px;
                margin-right: 10px;
                margin-left: 10px;
                background-color: rgba(245, 245, 245, 0.1);
                position: relative;

                @include tabletMedium {
                    background-color: rgba(24, 24, 24, 0.1);
                }

                span {
                    display: block;
                    width: 0;
                    height: 2px;
                    background-color: $accent;
                    position: absolute;
                    left: 0;
                    top: 0;
                    z-index: 4;

                    &.in-progress {
                        animation: fullwidth 10.3s linear infinite;
                    }
                }


            }

        }

        .btn-wr {
            right: 103px;
            bottom: 47px;

            @include desktop {
                right: 45px;
                bottom: 27px;
            }

            @include mobile {
                bottom: 15px;
                right: 16px;
            }
        }

        .main-controls {
            position: absolute;
            bottom: 40px;
            left: 100px;

            @include desktop {
                left: 45px;
                bottom: 20px;
            }

            @include mobile {
                left: 16px;
                bottom: 20px;
            }

            button {
                background: $accent;

                &:hover {
                    background: $dark;
                }
            }
        }
    }
}


// contacts sections styles
.contact {
    background: $dark;
    padding: 3em 0em;
    position: relative;

    .container {
        &:before {
            content: '';
            display: block;
            width: 819px;
            height: 232px;
            position: absolute;
            right: 0;
            top: -113px;
            background: url('../img/contact/contacts-bg.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: top center;
            z-index: 1;
        }
    }


    &__top {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 125px;
        padding-top: 150px;

        @include customTwo {
            margin-bottom: 10px;
            padding-top: 100px;
            justify-content: flex-start;
            flex-direction: column-reverse;
            flex-wrap: wrap;
            align-items: flex-start;

        }

