@keyframes scaleUp {
    0% {
        transform: translate(-50%, 0%) scale(0);
    }

    100% {
        transform: translate(-50%, 0%) scale(1);
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fullwidth {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(40px);
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes dash {
    from {
        stroke-dashoffset: 822;
    }

    to {
        stroke-dashoffset: 0;
    }
}

@keyframes full {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}



