.policy {
    &__content {
        .container {
            position: relative;
            padding-top: 4.5em;
            padding-bottom: 4em;

            @include tabletMedium {
                padding-top: 3.5em;
            }

            @include tabletLow {
                padding-top: 5.5em;
                padding-bottom: 3em;
            }
        }

        .back__link {
            left: 35px;

            @include tabletMedium {
                top: -5px;
            }

            @include tablet {
                top: 30px;
                left: 25px;
            }
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 100px;

            @include tabletMedium {
                line-height: 68px;
                font-size: 48px;
                margin-bottom: 0px;
            }

            @include tabletLow {
                line-height: 37px;
                font-size: 26px;
                margin-bottom: 32px;
            }
        }

        ul {
            list-style: none;
            list-style-image: none;
            margin: 20px auto 20px;

            li {
                font-family: $gt-reg;
                line-height: 28px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;
                position: relative;
                padding-left: 25px;

                &:before {
                    content: '';
                    display: block;
                    width: 5px;
                    height: 5px;
                    background-color: $dark;
                    border-radius: 50%;
                    position: absolute;
                    left: 0;
                    top: 12px;
                }
            }
        }

        h2 {
            font-family: $font-bold;
            line-height: 34px;
            font-size: 30px;
            letter-spacing: 0.03em;
            color: $dark;
            margin: 30px auto 20px;

            @include tabletLow {
                font-size: 20px;
                line-height: 28px;
            }
        }

        h3,
        h4,
        h5,
        h6 {
            font-family: $font-bold;
            line-height: 35px;
            font-size: 20px;
            letter-spacing: 0.03em;
            color: $dark;
            margin: 35px auto 13px;

            @include tabletLow {
                font-size: 18px;
                line-height: 22px;
            }
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;
            margin: 0 auto 20px;

            a {
                text-decoration: none;
                color: $accent;
                transition: color 0.4s;

                &:hover {
                    color: $hovered;
                }

            }

            b,
            strong {
                font-weight: 600;
                color: $accent;
            }
        }
    }
}
