body {
    line-height: 1.4;
    font-family: $font-reg;
    font-weight: 400;
    color: $udark;
    min-height: 100vh;
    background: $white;
}

button,
a {
    cursor: pointer;
}

.overlay {}

.stop-scrolling {
    overflow: hidden;
    height: 100vh;
    width: 100vw;
}

.container {
    width: 100%;
    max-width: 1300px;
    padding: 0 30px;
    margin: 0 auto;

    @include tabletLow {
        padding: 0px 15px;
    }
}

.hidden {
    display: none;
}


p,
h1,
h2,
h3,
h4,
h5 h6,
a,
span,
div,
li {
    &::selection {
        color: $white;
        background-color: $accent;
    }

}

main {
    padding-top: 142px;
    display: block;
    overflow: hidden;
    min-height: 90vh;

    @include desktopLow {
        padding-top: 90px;
    }

    @include mobile {
        padding-top: 68px;
        min-height: 1px;
    }

}

.main-controls {
    width: 114px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mobile {
        width: 75px;
    }

    button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 47px;
        height: 47px;
        position: relative;
        background: $dark;
        border-radius: 50%;
        transition: all 0.4s;

        @include mobile {
            width: 32px;
            height: 32px;
        }

        &:before {
            content: '';
            display: block;
            position: relative;
            width: 12px;
            height: 12px;
        }

        &.left-swipe {
            &:before {
                left: 1px;
                top: -1px;
                background: url('../img/icons/arrow-left.svg');
                background-size: cover;
                background-repeat: no-repeat;
            }
        }

        &.right-swipe {
            &:before {
                right: 1px;

                background: url('../img/icons/arrow-right.svg');
                background-size: cover;
                background-repeat: no-repeat;
            }
        }

        &:hover {
            background: $hovered;
        }
    }

}

.swiper-container {
    z-index: 10;
}

.btn-wr {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em;
    position: absolute;
    font-family: $font-bold;
    font-size: 12px;
    text-align: left;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $udark;
    text-decoration: none;
    transition: all 0.4s;

    svg {
        position: relative;
        z-index: 2;
        margin-left: 10px;
        transition: all 0.4s;
    }

    svg path {
        fill: $udark;
    }

    &:after {
        content: '';
        display: block;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: $accent;
        position: absolute;
        right: 0px;
        bottom: 4px;
        z-index: 0;
        transition: all 0.4s;

    }

    &:hover,
    &:focus {
        color: $accent;

        svg {
            transform: translateX(11px)
        }

        &:after {
            transform: translateX(11px)
        }
    }
}

.preloader {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    background: $white url('../img/popups/preloader-bg.svg');
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;

    @include mobile {
        padding: 0px 15px;
    }

    svg {
        position: relative;

        @include mobile {
            width: 100%;
            height: auto;
        }

        path {
            position: absolute;
        }

        .draw-ordinary {
            z-index: 3;
        }

        .draw-main {
            z-index: 10;
        }
    }
}

.back__link {
    line-height: 30px;
    font-size: 18px;
    letter-spacing: .03em;
    color: #000;
    opacity: .5;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    text-decoration: none;
    transition: all .3s ease;
    position: absolute;
    top: 0;
    left: 30px;
    z-index: 10;

    path {
        fill: black;
    }

    span {
        margin-left: 20px;
    }

    &:hover {
        opacity: 1;
    }

}

.cases__next--btn {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-left: calc(8.5% + 200px);

    @include tabletMedium {
        padding-left: 100px;
    }

    @include tabletLow {
        padding-left: 0;
    }

    span {
        font-family: $font-reg;
        line-height: 30px;
        font-size: 18px;
        letter-spacing: 0.03em;
        color: $white;
        margin-bottom: 10px;

        @include tabletLow {
            line-height: 26px;
            font-size: 16px;
        }
    }

    a {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font-family: $font-bold;
        line-height: 51px;
        font-size: 36px;
        letter-spacing: 0.03em;
        color: $white;
        text-decoration: none;
        position: relative;

        @include tabletLow {
            line-height: 37px;
            font-size: 26px;
        }


        span {
            display: inline-flex;
            margin-right: 20px;
            margin-bottom: 0;
            font-family: inherit;
            font-size: inherit;
            line-height: inherit;
            transition: color 0.4s;
            width: calc(100% - 75px);

            @include mobile {
                width: calc(100% - 77px);
            }

        }

        svg {
            position: relative;
            z-index: 2;
            margin-left: 10px;
            transition: all 0.4s;
        }

        svg path {
            fill: $white;
        }

        &:after {
            content: '';
            display: block;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: $accent;
            position: relative;
            left: -17px;
            z-index: 0;
            transition: all 0.4s;

        }

        &:hover,
        &:focus {
            span {
                color: $accent;
            }


            svg {
                transform: translateX(11px)
            }

            &:after {
                transform: translateX(11px)
            }
        }
    }

}

.js-loadme {
    // opacity: 0;
    // transition: opacity 0.35s;
    // transition-delay: 0.3s;

    &.js-ie {
        opacity: 1;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        transition: transform 0.4s;

        &:hover {
            transform: translateY(-40px);
        }
    }

    // &.is-init {
    //     opacity: 1;
    // }
}

.js-portfolio-loadme {
    &.js-ie {
        opacity: 1;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
    }
}

.js-lazy {
    opacity: 0;

    &.is-init {
        animation-name: fadeInImage;
        animation-duration: 400ms;
        animation-fill-mode: forwards;
        animation-timing-function: ease-in;
    }

    @keyframes fadeInImage {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }
}

.video {
    max-width: 905px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-left: calc(100% - 935px);
    margin-top: 30px;
    margin-bottom: 84px;

    @include tabletMedium {
        margin: 30px auto 65px;
        flex-direction: column;
    }

    &__block {
        width: calc(100% - 413px);
        min-height: 313px;
        position: relative;
        background-color: $dark;

        @include tabletMedium {
            width: 100%;
            min-height: 395px;
        }

        @include tabletLow {
            min-height: 275px;
        }

        @include mobile {
            min-height: 210px;
        }

        &--link {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        &--media {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: none;
        }

        &--btn {
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 1;
            display: none;
            padding: 0;
            width: 70px;
            height: 70px;
            border: none;
            outline: none;
            border-radius: 50%;
            background-color: $accent;
            transform: translate(-50%, -50%);
            cursor: pointer;
            transition: all .4s;

            svg {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            path {
                fill: $white;
            }

            &:hover {
                background-color: $hovered;
            }
        }

        &.video--enabled {
            cursor: pointer;
        }

        &.video--enabled &--btn {
            display: block;
        }

    }

    &__text {
        width: 413px;
        background-color: $accent;
        padding: 24px;
        font-family: $gt-reg;
        font-weight: 500;
        font-size: 20px;
        line-height: 35px;
        letter-spacing: 0.03em;
        color: $white;

        @include tabletMedium {
            width: 100%;
        }

        @include mobile {
            padding: 15px;
        }
    }

}
