.pricing {
    // min-height: 100vh;

    // @include tabletLow {
    //     min-height: 30vh;
    // }

    &__intro {
        padding-top: 5em;
        margin-bottom: 135px;

        @include tabletLow {
            padding-top: 2.5em;
            margin-bottom: 27px;
        }

        .container {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-wrap: wrap;

            @include tabletLow {
                flex-direction: column;
            }
        }

        h1 {
            max-width: 480px;
            width: 100%;
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-right: 10%;

            @include tabletMedium {
                margin-right: 0;
                max-width: 360px;
                line-height: 68px;
                font-size: 48px;
            }

            @include tabletLow {
                max-width: 100%;
                line-height: 45px;
                font-size: 32px;
                margin-bottom: 32px;
            }
        }

        p {
            width: calc(100% - 10% - 480px);
            padding-right: 25%;
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;

            @include desktopLow {
                padding-right: 0;
            }

            @include tabletMedium {
                width: calc(100% - 360px);
                line-height: 28px;
            }

            @include tabletLow {
                width: 100%;
            }
        }
    }

    &__form {
        .form__fields input {
            color: $some;
            border-color: $some;
        }

        .form__fields textarea {
            color: $some;
            border-color: $some;
        }

        .form__fields label {
            color: $some;
        }

        .form__fields button[type=submit] {
            background: $some;

            &:hover {
                background: $hovered;
            }
        }
    }
}

.page-id-786 .pricing__intro .container {
    h1 {
        @include tabletMedium {
            max-width: 480px;
        }
    }

}
