.select-field {
    .container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        position: relative;
        padding-right: 7em;
        min-height: calc(100vh - 143px);

        @include tabletMedium {
            min-height: 600px;
            padding: 0px 15px;
        }
    }

    &__nav {
        width: calc(100% - 575px);
        position: relative;
        margin-right: 45px;

        @include tabletMedium {
            width: 100%;
            margin: 0px auto;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        li {
            margin-bottom: 40px;
            transition: all 0.4s;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            position: relative;

            &:before {
                content: '';
                display: block;
                position: absolute;
                width: 500%;
                height: 100%;
                opacity: 0;
                top: 0;
                background-color: #F5F5F5;
                left: 0;
                transform: translateX(-100%);
                transition: all 0.35s;
                transition-delay: 0.1s;

                @include tabletMedium {
                    display: none;
                }
            }

            &:after {
                content: '';
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                opacity: 0;
                background-color: #F5F5F5;
                transition: all 0.35s;
                transition-delay: 0.1s;
                z-index: 0;

                @include tabletMedium {
                    display: none;
                }
            }

            a {
                display: block;
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                letter-spacing: 0.03em;
                color: $dark;
                text-decoration: none;
                text-align: left;
                padding: 0;
                transition: all 0.4s;
                position: relative;
                width: 100%;
                z-index: 5;



                span {
                    position: relative;
                    transition: all 0.4s;

                    @include tabletMedium {
                        display: block;
                        width: 100%;
                        text-align: center;
                    }
                }
            }

            &:hover {

                &:before,
                &:after {
                    opacity: 1;
                }

                a {
                    padding-top: 2.35em;
                    padding-bottom: 2.35em;
                    transform: translate(0px, 0px);
                    // background-color: #F5F5F5;

                    @include tabletMedium {
                        padding: 0em 2em;
                        background-color: $white;
                        color: $accent;
                        transform: scale(1.2);
                    }

                    span {
                        font-size: 56px;

                        @include tabletMedium {
                            font-size: 30px;
                        }
                    }
                }
            }
        }



    }

    &__image {
        max-width: 530px;
        width: 100%;
        min-height: calc(100vh - 143px);
        height: 100%;
        position: relative;

        @include tabletMedium {
            display: none;
        }

    }

    &--item {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: all 0.4s;

        &.is-visible {
            opacity: 1;
        }

        img {
            display: block;
            max-height: 530px;
        }
    }

    &--item:last-of-type {

        &:before {
            content: '';
            display: block;
            width: 530px;
            height: 530px;
            background: #F5F5F5;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 1;
            border-radius: 50%;
        }

        img {
            display: block;
            max-height: 650px;
            position: relative;
            top: 0px;
            z-index: 10;
        }

        canvas#newWave,
        canvas#musicWave {
            display: block;
            width: 95%;
            height: 300px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 4;
        }

        canvas#newLineWaves {
            display: block;
            position: absolute;
            top: 75%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 4;
        }
    }

    .data-item-3 {
        img {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .road-image {
            &:before {
                content: '';
                display: block;
                width: 370px;
                height: 370px;
                border-radius: 50%;
                background: #FFFFFF;
                opacity: 0.5;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: 5;
            }
        }

        #main-road {
            position: absolute;
            z-index: 9;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

.select-field.technologies .select-field--item:last-of-type {
    &:before {
        display: none;
    }
}

.select-field.technologies {
    .container {
        @include desktopLow {
            padding: 0px 30px;
        }

        @include tabletCustom {
            min-height: calc(100vh - 200px);

        }
    }

    .select-field__image {
        @include tabletMedium {
            max-width: 400px;
            width: 100%;
            display: block;
        }

        @include tabletCustom {
            display: none;
        }

    }

    .select-field--item {
        @include tabletMedium {
            width: 100%;

            img {
                display: block;
                max-width: 100%;
            }
        }




    }

    .select-field__nav {
        @include tabletMedium {
            width: calc(100% - 450px);
            margin-right: 50px;
            align-items: flex-start;
            justify-content: flex-start;
        }

        @include tabletCustom {
            width: 100%;
            margin-right: 0;
        }

        li.before-link {

            @include tabletMedium {
                display: none;
            }
        }

        li {
            span {
                @include tabletMedium {
                    text-align: left;
                }
            }

            &:hover {
                a {
                    @include tabletMedium {
                        transform: scale(1);
                        padding: 0;
                    }
                }

                span {
                    @include tabletMedium {
                        color: $accent;
                    }
                }
            }

        }
    }

}

.page-id-449 .select-field__nav li a,
.page-id-657 .select-field__nav li a {
    font-size: 26px;
}

.page-id-449 .select-field__nav li:hover a span,
.page-id-657 .select-field__nav li:hover a span {
    font-size: 36px;
}
