.question-answer {

    &__intro {
        padding-top: 4em;
        margin-bottom: 70px;
        position: relative;

        @include desktopLow {
            padding-top: 6em;
        }

        @include tabletMedium {
            padding-top: 4em;
            margin-bottom: 40px;

        }

        @include tabletLow {
            padding-top: 50px;
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            position: relative;
            z-index: 5;

            @include tabletMedium {
                line-height: 68px;
                font-size: 48px;
            }

            @include tabletLow {
                line-height: 45px;
                font-size: 32px;
            }
        }

        &--bg {
            width: 830px;
            height: 280px;
            position: absolute;
            z-index: 0;
            left: 64.5%;
            top: -142px;
            transform: translate(-55%, 0%);
            background: url('../img/q&a/q&a-bg.svg');
            background-size: cover;
            background-repeat: no-repeat;

            @include desktopLow {
                left: 70%;
                top: -90px;
            }

            @include tabletMedium {
                top: -50px;
                left: 85%;
            }

            @include tabletLow {
                top: -20px;
                left: 75;
                transform: translate(-50%, 0%);
                width: 415px;
                height: 140px;
            }


        }
    }

    &__main {
        @include tabletLow {
            padding-bottom: 30px;
        }

        .container {
            @include tabletMedium {
                padding-right: 0;
            }
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        margin-bottom: 70px;
        max-width: 90%;
        background: transparent;
        padding-left: 100px;
        padding-right: 5em;
        position: relative;
        transition: all 0.4s;

        @include tabletMedium {
            padding-left: 0;
            padding-right: 0;
            max-width: 100%;
        }

        @include tabletLow {
            margin-bottom: 32px;
        }

        &.is-show {
            background: #F5F5F5;

            &::before {
                background: #F5F5F5;
            }
        }

        &:before {
            content: '';
            display: block;
            width: 3000px;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            transform: translate(-100%, 0%);
            background: transparent;
            transition: all 0.4s;
        }

        &--headline {
            display: flex;
            transition: all .4s;
            position: relative;
            cursor: pointer;

            @include tabletMedium {
                padding-right: 30px;
            }

            @include tabletLow {
                padding-right: 15px;
            }

            p {
                font-family: $font-med;
                line-height: 34px;
                font-size: 24px;
                letter-spacing: 0.03em;
                color: $dark;
                transition: all .4s;

                @include tabletLow {
                    line-height: 25px;
                    font-size: 18px;
                }
            }

            &:after {
                content: '';
                display: inline-block;
                vertical-align: middle;
                position: relative;
                margin-left: 30px;
                width: 0;
                height: 0;
                border: 0 solid transparent;
                border-left-width: 5px;
                border-right-width: 5px;
                border-top: 8px solid #000;
                top: 13px;
                transition: all .4s;

                @include tabletLow {
                    border-left-width: 4px;
                    border-right-width: 4px;
                    border-top: 7px solid #000;
                    top: 9px;
                }
            }

            &:hover {
                p {
                    color: $accent;
                }

                &:after {
                    border-top-color: #ff634e;

                }
            }

            &.is-opened {

                padding-top: 70px;

                @include tabletLow {
                    padding-top: 16px;
                }

                p {
                    color: $accent;
                }

                &:after {
                    border-top-color: #ff634e;
                    transform: rotate(-180deg);
                }

            }
        }



        &--text {
            margin-top: 30px;
            padding-left: 105px;
            margin-bottom: 70px;

            @include tabletMedium {
                padding-left: 9%;
                padding-right: 9%;
            }

            @include tabletLow {
                padding-left: 0px;
                padding-right: 15px;
                margin-top: 16px;
                margin-bottom: 30px;
            }

            div,
            p {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 30px;

                @include tabletLow {
										line-height: 28px;
                }
            }

            li {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                color: $dark;
                margin-bottom: 14px;

                @include tabletLow {
                    line-height: 28px;
                }
            }

            ul {
                list-style: none;
                list-style-image: none;

                li {
                    position: relative;
                    list-style-type: none;
                    padding-left: 20px;

                    &:before {
                        content: '';
                        display: block;
                        width: 5px;
                        height: 5px;
                        background-color: $accent;
                        border-radius: 50%;
                        position: absolute;
                        left: 0;
                        top: 13px;
                    }
                }
            }

            ol {
                list-style: none;
                list-style-image: none;
                counter-reset: item;
                margin-bottom: 30px;

                li {
                    position: relative;
                    list-style-type: none;
                    padding-left: 20px;

                    &:before {
                        content: counter(item);
                        counter-increment: item;
                        color: $accent;
                        font-family: $font-med;
                        line-height: 26px;
                        font-size: 15px;
                        position: absolute;
                        left: 0px;
                        top: 1px;
                    }
                }
            }
        }
    }


}

.contact.faq .container:before {
    display: none;
}

.contact.faq .contact__top {
    padding-top: 0;

    @include tabletCustom {
        flex-direction: column;
    }

    p:last-of-type {
        display: none;
    }
}
