.mission {
    background: $white;

    @include desktopLow {
        padding-top: 50px;
    }

    @include tabletMedium {
        padding-top: 80px;
    }

    @include tabletLow {
        padding-top: 41px;
    }

    p {
        color: $dark;
    }

    .container {
        position: relative;
    }

    &__main-text {
        position: relative;
        z-index: 5;

        h1 {
            font-family: $gt-reg;
            line-height: 46px;
            font-size: 24px;
            letter-spacing: 0.05em;
            color: $accent;
            margin-bottom: 16px;

            @include tabletLow {
                font-size: 20px;
                line-height: 34px;
            }
        }

        p {
            max-width: 930px;
            width: 100%;
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            margin-bottom: 40px;

            @include tabletLow {
                font-size: 26px;
                line-height: 37px;
            }
        }
    }

    &__decription {
        position: relative;
        z-index: 5;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-bottom: 111px;
        padding-right: 103px;

        @include tabletMedium {
            margin-bottom: 30px;
            padding-right: 0;
        }

        p {
            max-width: 520px;
            width: 100%;
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            margin-bottom: 25px;
        }
    }

    &__link {
        max-width: 520px;
        min-height: 40px;
        width: 100%;
        margin-left: calc(100% - 633px);
        position: relative;
        z-index: 5;

        @include tabletMedium {
            margin-left: calc(100% - 535px);
        }

        @include tabletLow {
            margin: 0;
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
        }
    }

    &__bg {
        img {
            display: block;
            width: 1052px;
            height: 750px;
            position: absolute;
            left: -184px;
            top: 53px;
            z-index: 1;

            @include tabletMedium {
                width: 936px;
                height: 667px;
            }

            @include tabletLow {
                width: 525px;
                height: 374px;
                left: -50%;
                top: 38px;
            }

        }
    }
}

.know {
    position: relative;
    z-index: 10;
    color: $dark;
    letter-spacing: 0.03em;
    padding-bottom: 6.5em;

    .contacts__figure {
        margin-bottom: 50px;
    }

    @include tabletMedium {
        padding-top: 30px;
        padding-bottom: 3em;
    }

    // @include tabletLow {
    //     padding-bottom: 3em;
    // }

    h2 {
        max-width: 520px;
        width: 100%;
        font-family: $font-bold;
        line-height: 56px;
        font-size: 40px;
        margin: 150px 0px 100px;

        @include tabletMedium {
            margin: 120px 0px 40px;
        }
    }

    &__text {
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;

        @include tabletMedium {
            justify-content: center;
            align-items: center;
            flex-direction: column;
        }

        &--inner {
            max-width: 420px;
            width: 100%;
            margin-left: 104px;

            @include tabletMedium {
                max-width: 520px;
                margin: 0px auto 40px;
            }

            p {
                font-family: $gt-reg;
                line-height: 30px;
                font-size: 16px;
                letter-spacing: 0.03em;
                margin-bottom: 25px;

                @include tabletMedium {
                    margin-bottom: 40px;
                }
            }

            p:last-of-type {
                margin-bottom: 0px;
            }
        }

        &--inner:first-of-type {
            margin-left: 0;

            @include tabletMedium {
                margin: 0px auto 40px;
            }
        }
    }
}

.projects {
    background-color: $dark;
    color: $white;
    letter-spacing: 0.03em;
    padding: 9.5em 0em;

    @include tabletMedium {
        padding: 5em 0em;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 60px;

        @include tabletMedium {
            margin-bottom: 70px;
        }

        @include tabletLow {
            margin-bottom: 0px;
        }
    }

    h2 {
        font-family: $font-bold;
        line-height: 56px;
        font-size: 40px;

        @include tabletLow {
            font-size: 26px;
            line-height: 37px;
        }
    }


    .main-controls button {
        background: $accent;
        position: relative;

        @include tabletLow {
            display: none;
        }

        &.swiper-button-disabled {
            cursor: default;
        }

        &:hover {
            background: $hovered;
        }
    }

    .view-link {
        display: block;
        width: 178px;
        height: 47px;
        background-color: $accent;
        border-radius: 100px;
        font-family: $font-bold;
        font-size: 12px;
        line-height: 47px;
        text-align: center;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        text-decoration: none;
        color: inherit;
        transition: all 0.4s;
        margin: 51px auto 0px;

        @include tabletMedium {
            margin: 80px auto 0px;

        }

        @include tabletLow {
            margin-top: 41px;
            width: 248px;
        }


        &:hover {
            background-color: $hovered;
        }
    }

    &__slider {
        padding-top: 60px;
    }

    &__item {
        display: block;
        width: 496px;
        height: 350px;
        position: relative;
        color: inherit;
        text-decoration: none;

        @include desktop {
            width: 472px;
            height: 333px;
            margin-right: 24px;

            &:last-of-type {
                margin-right: 0;
            }
        }

        @include tabletLow {
            width: 240px;
            height: 169px;
        }



        &--inner {
            width: 100%;
            height: 100%;
            position: relative;

            &:after {
                content: '';
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                background-color: rgba(0, 0, 0, 0.4);
                transition: all .4s;
                opacity: 1;
                z-index: 1;
            }

            &:hover::after {
                opacity: 0;
            }
        }


        &--info {
            position: absolute;
            left: 40px;
            bottom: 56px;
            z-index: 20;
            width: 85%;

            @include tabletLow {
                left: 16px;
                bottom: 16px;
            }

            p {
                font-family: $font-bold;
                line-height: 49px;
                font-size: 30px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;

                @include tabletLow {
                    font-size: 20px;
                    line-height: 33px;
                }
            }

            span {
                display: block;
                font-family: $gt-reg;
                line-height: 38px;
                font-size: 20px;

                @include tabletLow {
                    font-size: 16px;
                    line-height: 28px;
                }
            }
        }

        // &--image {
        // 	background-repeat: no-repeat;
        // 	background-position: center center;
        // 	background-size: cover;
        // 	width: 100%;
        // 	min-height: 350px;
        // }
    }

}

.blog {
    background-color: $dark;
    padding: 0em 0em 9.5em;

    @include tabletMedium {
        padding: 0em 0em 3em;
    }


    .container {
        position: relative;
    }

    h2 {
        font-family: $font-bold;
        line-height: 68px;
        font-size: 48px;
        color: $white;
        letter-spacing: 0.03em;
        margin-bottom: 70px;

        @include tabletLow {
            font-size: 26px;
            line-height: 37px;
            margin-bottom: 40px;
        }
    }

    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-right: 150px;

        @include tabletMedium {
            padding-right: 0;
            flex-wrap: wrap;
        }

        &--item {
            display: block;
            width: calc(100%/3 - 28px);
            margin-right: 55px;
            height: 352px;
            position: relative;
            text-decoration: none;

            @include tabletMedium {
                width: calc(100%/2 - 12px);
                margin-right: 24px;
                margin-bottom: 32px;

                &:nth-child(even) {
                    margin-right: 0;
                }
            }

            @include tabletLow {
                width: 100%;
                margin: 0px auto 32px;

                &:nth-child(even) {
                    margin: 0px auto 32px;
                }
            }

            // a {
            //     display: block;
            //     width: 100%;
            //     height: 100%;
            //     position: absolute;
            //     text-decoration: none;

            // }

        }

        &--item:last-of-type {
            margin-right: 0;
        }

        &--inner {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 4;

            &:after {
                content: '';
                display: block;
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.4);
                position: absolute;
                left: 0;
                top: 0;
                opacity: 1;
                transition: opacity 0.4s;
                z-index: 1;

            }

            &:hover::after {
                opacity: 0;
            }
        }

        &--text {
            position: absolute;
            z-index: 4;
            opacity: 1;
            left: 30px;
            bottom: 30px;
            width: 85%;
        }

        &--name {
            width: 100%;
            font-family: $font-bold;
            line-height: 30px;
            font-size: 18px;
            color: $white;
            letter-spacing: 0.03em;
            margin-bottom: 5px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        &--date {
            font-family: $font-reg;
            line-height: 20px;
            font-size: 16px;
            color: $white;
            letter-spacing: 0.03em;
        }

        &--link {
            position: absolute;
            right: 0;
            top: 63%;
            transform: translate(0%, -50%);
            color: $white;

            @include tabletMedium {
                position: relative;
                right: 0;
                left: 0;
                top: 0;
                transform: translate(0%, 0%);
                width: 145px;
                margin-left: calc(100% - 145px);
                margin-top: 20px;
            }

            svg path {
                fill: $white;
            }
        }
    }

}

.about-partners.partners {
    background-color: $dark;
    padding-bottom: 9.5em;

    @include tabletMedium {
        padding-bottom: 6em;
    }

    @include tabletLow {
        padding-bottom: 3em;
    }


    .partners__text {
        padding-top: 4em;

        h2 {
            color: $white;
        }
    }
}
