.trace-intro {

    &--inner {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;

        @include tabletMedium {
            flex-wrap: wrap;
            min-height: 640px;
        }

        @include customThr {
            flex-direction: column;
            min-height: 100px;
        }


    }

    &--text {
        width: calc(100% - 620px);
        margin-right: 45px;
        padding-top: 8em;

        @include tabletMedium {
            max-width: 620px;
            width: 100%;
            margin-right: 0;
            padding-top: 5em;
            position: absolute;
            left: 0;
            top: 0;
        }

        @include tabletCustom {
            position: relative;
            max-width: 100%;

        }

        @include tabletLow {
            position: relative;
            max-width: 100%;
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 30px;

            @include tabletMedium {
                font-size: 48px;
                line-height: 68px;
            }

            @include tabletCustom {
                font-size: 32px;
                line-height: 45px;
            }
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;

            @include tabletCustom {
                line-height: 28px;
            }
        }
    }

    &--image {
        width: 575px;
        padding-right: 3em;
        padding-top: 7em;
        position: relative;

        @include tabletMedium {
            width: 417px;
            position: absolute;
            padding-top: 0;
            bottom: 80px;
            right: 0;
            min-height: 350px;
        }

        @include tabletCustom {
            position: relative;
            max-width: 417px;
            width: 100%;
            margin: 80px auto;
            bottom: 0;
        }

        @include mobile {
            margin: 20px auto 30px;
            min-height: 310px;
        }


        img {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            @include tabletMedium {
                width: 100%;
            }

            @include mobile {
                max-width: 288px;
            }
        }

        .road-image {
            &:before {
                content: '';
                display: block;
                width: 370px;
                height: 370px;
                border-radius: 50%;
                background: #FFFFFF;
                opacity: 0.5;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: 5;

                @include tabletMedium {
                    width: 290px;
                    height: 290px;
                }

                @include mobile {
                    width: 200px;
                    height: 200px;
                }
            }

            img {
                @include tabletMedium {
                    max-width: 340px;
                    width: 100%;
                }

                @include mobile {
                    max-width: 235px;
                }
            }
        }

        #main-road {
            position: absolute;
            z-index: 9;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);

            @include tabletMedium {
                width: 100%;
            }
        }
    }
}

.register {
    .container {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        flex-direction: column;
        max-width: 1240px;
        padding-right: 65px;
        padding-left: 0px;

        @include desktop {
            max-width: 1300px;
            padding-left: 30px;
        }

        @include tabletMedium {
            padding-top: 5em;
            padding-bottom: 5em;
            padding-right: 40px;
        }


        @include tabletCustom {
            padding-top: 0em;
            padding-bottom: 1em;
        }

        @include tabletLow {
            padding-left: 15px;
            padding-right: 15px;
        }

        &:before {
            content: '';
            display: block;
            width: 400%;
            height: 100%;
            background-color: $dark;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;
        }
    }

    &__text {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;
        z-index: 5;
        margin-bottom: 118px;

        @include tabletMedium {
            flex-wrap: wrap;
            flex-direction: column;
            width: 100%;
        }

        @include tabletCustom {
            margin-bottom: 30px;
        }

        h2,
        h3,
        h4,
        h5,
        h6 {
            display: block;
            max-width: 420px;
            width: 100%;
            margin-right: 110px;
            font-family: $font-bold;
            line-height: 49px;
            font-size: 30px;
            letter-spacing: 0.03em;
            color: $white;

            @include desktop {
                margin-right: 80px;
            }

            @include tabletMedium {
                margin-bottom: 40px;
            }

            @include tabletCustom {
                margin: 0px 0px 30px 0px;
            }

            @include mobile {
                line-height: 33px;
                font-size: 20px;
            }
        }

        &--wrapp {
            width: calc(100% - 530px);
            display: flex;

            @include tabletMedium {
                width: 100%;
                justify-content: flex-end;
                align-items: flex-end;
            }
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $white;

            @include tabletMedium {
                max-width: 620px;
                width: 100%;
            }

            @include mobile {
                line-height: 28px;
            }
        }
    }

    &__inner {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        position: relative;
        z-index: 5;

        &:last-of-type {
            padding-bottom: 7em;

            @include tabletMedium {
                padding-bottom: 2em;
            }

            @include tabletCustom {
                flex-direction: column;
                align-items: flex-start;
                // justify-content: flex-start;
            }
        }
    }

    &__item {
        padding-top: 0.75em;

        @include tabletCustom {
            padding-top: 0;
            padding-left: 50px;
            margin-bottom: 61px;

        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $white;
            background-color: $dark;
        }

        &:first-of-type {
            padding-left: 15px;

            @include tabletCustom {
                padding-left: 50px;
            }
        }

        &:last-of-type {
            padding-right: 15px;

            @include tabletCustom {
                padding-right: 0;
                margin-bottom: 20px;
            }
        }

        &:nth-child(3) {
            padding-left: 35px;

            @include tabletCustom {
                padding-left: 50px;
            }
        }
    }

    &__rout {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        min-height: 34px;
        position: relative;
        z-index: 10;

        @include tabletCustom {
            position: absolute;
            left: 0;
            top: 0;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
        }
    }

    &--point {
        width: 34px;
        height: 34px;
        position: relative;

        @include tabletCustom {
            margin-bottom: 56px;
        }

        &:before {
            content: '';
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: $white;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: all 0.4s;
        }

        span {
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: $accent;
            position: absolute;
            z-index: 8;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: all 0.4s;
            opacity: 0;

            &:before {
                content: '';
                display: block;
                width: 34px;
                height: 34px;
                border-radius: 50%;
                background-color: $accent;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                opacity: 0.3;
                z-index: 5;
            }

            &:after {
                content: '';
                display: block;
                width: 19px;
                height: 19px;
                border-radius: 50%;
                background-color: $accent;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                opacity: 0.6;
                z-index: 6;
            }
        }

        &.active-point span {
            opacity: 1;
        }


    }

    &--separator {
        position: absolute;
        width: calc((100% - 230px)/3);
        height: 3px;

        @include tabletCustom {
            height: 42px;
            width: 2px;
        }

        &:after {
            content: '';
            display: block;
            width: 100%;
            height: 2px;
            position: absolute;
            background: url('../img/traceability/dots.svg') no-repeat;
            background-size: cover;
            z-index: -1;

            @include tabletCustom {
                width: 2px;
                height: 53px;
                background: url('../img/traceability/dots-adaptive.svg');
                background-size: cover;
            }
        }

        &:before {
            content: '';
            display: block;
            width: 0%;
            height: 2px;
            position: absolute;
            background-color: $accent;
            z-index: 3;
            transition: all 4s linear;

            @include tabletMedium {
                transition: all 3.7s linear;
            }

            @include tabletCustom {
                width: 2px;
                height: 0px;
                transition: all 2.7s linear;
            }
        }

        &.is-moving {
            &:before {
                width: 100%;

                @include tabletCustom {
                    height: 42px;
                }
            }
        }

        &:nth-of-type(2) {
            left: 50px;

            @include tabletCustom {
                left: 16px;
                top: 41px;
            }
        }

        &:nth-of-type(4) {
            left: calc((100% - 230px)/3 + 115px);

            @include tabletCustom {
                left: 16px;
                top: 131px;
            }
        }

        &:nth-of-type(6) {
            right: 50px;

            @include tabletCustom {
                left: 16px;
                top: 222px;
            }
        }
    }

    &__car {
        width: 32px;
        height: 19px;
        background: url('../img/traceability/car.svg');
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
        left: 0;
        top: -10px;
        z-index: 8;

        @include tabletCustom {
            display: none;
        }
    }
}
