.notfound {
    position: relative;
    min-height: calc(100vh - 144px - 105px);
    overflow: hidden;

    @include desktopLow {
        min-height: calc(100vh - 90px - 151px);
    }

    @include tablet {
        min-height: calc(100vh - 90px - 255px);
    }

    @include tabletLow {
        min-height: calc(100vh - 90px - 206px);
        overflow: initial;
        padding-bottom: 45px;
    }

    @include mobile {
        min-height: calc(100vh - 68px - 206px);
    }

    .container {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        // flex-wrap: wrap;
        padding-top: 5em;

        @include tabletMedium {
            flex-direction: column;
            padding-top: 1em;
        }
    }

    .back__link {
        left: 35px;

        @include desktopLow {
            top: 46px;
        }

        @include tablet {
            top: 35px;
        }

        @include tabletLow {
            left: 20px;
        }
    }

    &--text {
        max-width: 420px;
        width: 100%;

        @include tabletMedium {
            max-width: 100%;
        }

        p {
            font-family: $gt-reg;
            font-size: 16px;
            line-height: 30px;
            letter-spacing: 0.03em;
            color: rgba(24, 24, 24, 0.8);
            margin-bottom: 50px;

            @include tabletMedium {
                line-height: 28px;
            }

            @include tabletLow {
                margin-bottom: 0;
            }
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            padding-top: 1.25em;
            margin-bottom: 39px;

            @include tabletMedium {
                line-height: 68px;
                font-size: 48px;
                padding-top: 2.25em;
            }

            @include tabletLow {
                font-size: 32px;
                line-height: 45px;
                padding-top: 2.5em;
            }
        }



        a {
            width: 178px;
            display: block;
            height: 47px;
            background: #ff634e;
            border-radius: 100px;
            margin-right: 100px;
            text-decoration: none;
            font-size: 12px;
            font-family: "Raleway-Bold";
            line-height: 49px;
            text-align: center;
            letter-spacing: .1em;
            text-transform: uppercase;
            color: #fff;
            transition: all .4s;
            position: relative;

            @include tabletLow {
                display: none;
            }

            &.ie-btn {
                &:after {
                    display: none;
                }
            }

            &:after {
                content: '';
                display: block;
                width: 152px;
                height: 41px;
                background: #000000;
                opacity: 0.2;
                filter: blur(18px);
                border-radius: 100px;
                position: absolute;
                left: 50%;
                bottom: -10px;
                transform: translate(-50%, 0%);
                z-index: -1;

            }

            @include mobile {
                width: 248px;
                margin: 0px auto;
            }

            &:hover {
                background: $hovered;
            }
        }
    }

    &--image {
        width: calc(100% - 420px);
        min-height: 425px;
        position: relative;
        padding-top: 95px;

        @include tabletMedium {
            width: 100%;
            min-height: 460px;
        }

        @include tabletLow {
            min-height: 140px;
            padding-top: 70px;
        }

        &__animated {
            position: absolute;
            top: 50%;
            left: 54%;
            transform: translate(-50%, -50%);
            height: 360px;
            width: 590px;
            z-index: 5;

            @include tabletLow {
                height: calc(100% - 47px);
                width: 220px;
                top: 0;
                left: 50%;
                transform: translate(-50%, 0%);
            }
        }

        &__main {
            display: block;
            width: 239px;
            height: 239px;
            margin: 0px auto;

            @include tabletLow {
                margin-bottom: 68px;
                width: 100px;
                height: 100px;
            }
        }

        a {
            width: 178px;
            display: none;
            height: 47px;
            margin: 0px auto;
            background: #ff634e;
            border-radius: 100px;
            text-decoration: none;
            font-size: 12px;
            font-family: "Raleway-Bold";
            line-height: 49px;
            text-align: center;
            letter-spacing: .1em;
            text-transform: uppercase;
            color: #fff;
            transition: all .4s;
            position: relative;

            @include tabletLow {
                display: block;
            }

            &.ie-btn {
                &:after {
                    display: none;
                }
            }

            &:after {
                content: '';
                display: block;
                width: 152px;
                height: 41px;
                background: #000000;
                opacity: 0.2;
                -webkit-filter: blur(18px);
                filter: blur(18px);
                border-radius: 100px;
                position: absolute;
                left: 50%;
                bottom: -10px;
                transform: translate(-50%, 0%);
                z-index: -1;

            }

            @include mobile {
                width: 248px;
                margin: 0px auto;
            }

            &:hover {
                background: $hovered;
            }
        }
    }
}

.js-animated {
    position: absolute;

    &:nth-child(1) {
        left: 0px;
        top: 98px;

        @include tabletLow {
            width: 51px;
            height: 59px;
            left: -5px;
            top: 94px;
        }
    }

    &:nth-child(2) {
        right: 57px;
        top: 63px;

        @include tabletLow {
            width: 44px;
            height: 58px;
            right: 0px;
            top: 62px;
        }
    }

    &:nth-child(3) {
        right: 162px;
        bottom: 6px;

        @include tabletLow {
            width: 22px;
            height: 25px;
            right: 46px;
            bottom: 56px;

        }
    }

    &:nth-child(4) {
        left: 188px;
        top: -19px;

        @include tabletLow {
            width: 12px;
            height: 14px;
            left: 79px;
            top: 45px;
        }
    }

    &:nth-child(5) {
        left: 111px;
        bottom: 1px;

        @include tabletLow {
            width: 12px;
            height: 14px;
            left: 45px;
            bottom: 61px;
        }
    }
}
