$desktop: 1400px;
$desktopLow: 1260px;
$tabletMedium: 991px;
$tablet: 767px;
$tabletLow: 630px;

$mobile: 480px;
$mobile-small: 320px;
$mobile-width: 320px;
$tablet-width: 630px;

$ultrasmall-screen: 620px;
$small-screen: 750px;

@mixin desktop {
    @media (max-width: #{$desktop}) {
        @content;
    }
}

@mixin desktopLow {
    @media (max-width: #{$desktopLow}) {
        @content;
    }
}

@mixin tabletMedium {
    @media (max-width: #{$tabletMedium}) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: #{$tablet}) {
        @content;
    }
}

@mixin tabletCustom {
    @media (max-width: #{$tablet - 22px}) {
        @content;
    }
}

@mixin tabletLow {
    @media (max-width: #{$tabletLow}) {
        @content;
    }
}

@mixin mobile {
    @media (max-width: #{$mobile}) {
        @content;
    }
}

@mixin mobile-small {
    @media (max-width: #{$mobile-small}) {
        @content;
    }
}

@mixin mix {
    @media (max-height: #{$ultrasmall-screen}) and (max-width: #{$desktop}) {
        @content;
    }
}

@mixin mixLarge {
    @media (max-height: #{$small-screen}) and (min-width: #{$desktop}) {
        @content;
    }
}

@mixin custom {
    @media (min-width: #{$mobile-width}) and (max-width: #{$tablet-width - 1px}) {
        @content;
    }
}

@mixin customTwo {
    @media (min-width: #{$mobile-width}) and (max-width: #{$tablet + 100px}) {
        @content;
    }
}


@mixin customThr {
    @media (min-width: #{$mobile-width}) and (max-width: #{$tablet - 22px}) {
        @content;
    }
}

@mixin height {
    @media (max-height: #{$small-screen}) {
        @content;
    }
}

@mixin heightUltra {
    @media (max-height: #{$ultrasmall-screen}) {
        @content;
    }
}

@mixin mixLow {
    @media (max-height: #{$ultrasmall-screen}) and (min-width: #{$desktop}) {
        @content;
    }
}
