.popup {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: none;
    z-index: 300;
}

.hideTrigger {
    opacity: 0;
    position: absolute;
    bottom: 0;
    left: 0;
}

.close-popup {
    width: 47px;
    height: 47px;
    background-color: $accent;
    border-radius: 50%;
    position: absolute;
    right: 20px;
    top: 20px;
    transition: all 0.4s;
    z-index: 10;

    @include mobile {
        right: 15px;
        top: 45px;
    }

    &:hover {
        background-color: $hovered;
    }

    span {
        width: 16px;
        height: 2px;
        background-color: $white;
        border-radius: 10px;
        position: absolute;
        left: 50%;
        top: 50%;
        transition: all 0.4s;
    }

    span:first-of-type {
        transform: translate(-50%, -50%) rotate(-45deg);
    }

    span:last-of-type {
        transform: translate(-50%, -50%) rotate(45deg);
    }
}

.contact-popup {
    background-color: rgba(24, 24, 24, 0.4);

    @include tablet {
        max-height: 100%;
        height: 100%;
        overflow: scroll;
    }

    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 100vh;
    }

    h4 {
        max-width: 420px;
        width: 100%;
        font-family: $gt-reg;
        line-height: 33px;
        font-size: 20px;
        letter-spacing: 0.03em;
        color: #FFFFFF;
        margin-bottom: 30px;
    }

    .form__fields--short {
        flex-direction: column;
        justify-content: center;
    }

    .form__fields--short .form__fields--item {
        width: 100%;
        margin: 0px auto 50px;
    }

    .form__fields--short .form__fields--item:first-of-type {
        margin: 0px auto 50px;
    }

    .form__fields--long:last-of-type {
        margin-bottom: 0;
    }


    &__form {
        max-width: 826px;
        width: 100%;
        margin: 0px auto;
        padding: 3.8em 6.2em;
        background-color: $dark;
        position: relative;
        transform: translateY(40px);
        opacity: 0;
        transition: all 0.2s linear;

        @include tabletMedium {
            padding: 3.8em 4em;
        }

        @include mobile {
            padding: 6em 1.5em 2.5em;
        }

        &:before {
            content: '';
            display: block;
            width: 383px;
            height: 172px;
            background: url('../img/popups/lines.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 0;
        }
    }

    &__form.sUp {
        opacity: 1;
        transform: translateY(0px);
    }
}

.thanks-popup {
    background-color: rgba(24, 24, 24, 0.4);

    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 100vh;
        padding: 0px 15px;
    }

    &__inner {
        position: relative;
        background-color: $dark;
        padding: 3.8em 6.2em;
        max-width: 826px;
        width: 100%;
        margin: 0px auto;
        position: relative;
        transform: translateY(40px);
        opacity: 0;
        transition: all 0.2s linear;

        &.sUp {
            opacity: 1;
            transform: translateY(0px);
        }

        &:before {
            content: '';
            display: block;
            width: 383px;
            height: 172px;
            background: url('../img/popups/lines.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 0;
        }

        &--btn {
            button {
                width: 178px;
                height: 47px;
                background: $accent;
                border-radius: 100px;
                outline: none;
                font-size: 12px;
                font-family: $font-bold;
                text-align: center;
                letter-spacing: 0.1em;
                text-transform: uppercase;
                color: $white;
                transition: all 0.4s linear;

                &:hover {
                    background: $hovered;
                }
            }
        }
    }

    h4 {
        max-width: 415px;
        width: 100%;
        font-family: $font-bold;
        line-height: 49px;
        font-size: 30px;
        letter-spacing: 0.03em;
        color: $white;
        margin-bottom: 30px;
    }

    p {
        max-width: 420px;
        width: 100%;
        font-family: $gt-reg;
        line-height: 35px;
        font-size: 20px;
        letter-spacing: 0.03em;
        color: $white;
        margin-bottom: 60px;
    }


}
