.intro-anti {
    &__inner {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;

        @media (max-width: 630px) {
            flex-direction: column;
        }


    }
}


.description {
    margin-top: 71px;

    .container {
        max-width: 1240px;
        background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        @include desktop {
            background: transparent;
            position: relative;
            max-width: 1300px;

            &:after {
                content: '';
                display: block;
                width: calc(100% - 30px);
                height: 100%;
                position: absolute;
                right: 0;
                top: 0;
                background: linear-gradient(270deg, #F5F5F5 0%, rgba(245, 245, 245, 0) 139.31%);
                z-index: -1;
            }
        }

        @include tabletMedium {
            flex-direction: column;
        }

        @include mobile {
            &:after {
                width: calc(100% - 15px);
            }
        }
    }

    &__title {
        padding: 40px 0px 40px 40px;
        max-width: 400px;
        width: 100%;
        margin-right: 100px;

        @include tabletMedium {
            // max-width: 100%;
            padding-bottom: 0;
            margin: 0;

        }

        h2 {
            font-family: $gt-med;
            line-height: 35px;
            font-size: 20px;
            letter-spacing: 0.03em;
            color: $accent;
        }
    }

    &__text {
        width: calc(100% - 500px);
        padding: 40px 10px 40px 0px;

        @include tabletMedium {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            padding-left: 40px;
            padding-top: 20px;
        }

        @include mobile {
            padding-right: 0;
        }



        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;

            @include tabletMedium {
                max-width: 620px;
                width: 100%;
                line-height: 28px;
            }
        }
    }


}

.anti__commun {
    background-color: $white;
    position: relative;

    .container {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        padding-top: 8em;
        padding-bottom: 8em;

        @include tabletMedium {
            padding-top: 5em;
            padding-bottom: 5em;
            flex-direction: column;
        }

        @include mobile {
            padding-top: 3.8em;
            padding-bottom: 1em;
        }

        &:before {
            content: '';
            display: block;
            width: 400%;
            height: 100%;
            background-color: $dark;
            position: absolute;
            right: 30px;
            top: 0;
            z-index: 1;

            @include desktop {
                right: 0;
            }
        }
    }

    &--title {
        max-width: 420px;
        margin-right: 110px;
        position: relative;
        z-index: 5;

        @include desktop {
            margin-right: 80px;
        }

        @include tabletMedium {
            margin-right: 0;
            margin-bottom: 40px;
        }

        @include mobile {
            margin-bottom: 32px;
        }

        h3 {
            font-family: $font-bold;
            line-height: 56px;
            font-size: 40px;
            letter-spacing: 0.03em;
            color: $white;

            @include mobile {
                font-size: 26px;
                line-height: 37px;
            }
        }
    }

    &--text {
        width: calc(100% - 530px);
        position: relative;
        z-index: 5;
        padding-right: 6em;

        @include desktop {
            padding-right: 0;
        }

        @include tabletMedium {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            flex-direction: column;
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $white;
            margin-bottom: 25px;

            @include tabletMedium {
                max-width: 620px;
                width: 100%;
            }

            @include mobile {
                margin-bottom: 20px;
            }

            &:last-of-type {
                margin-bottom: 0;
            }
        }

    }


}

.scan {
    background-color: $white;
    position: relative;


    .container {
        position: relative;
        padding-bottom: 8em;

        @include tabletMedium {
            padding-top: 20px;
        }

        @include mobile {
            padding-left: 15px;
            padding-top: 0;
            padding-bottom: 6em;
        }


        &:before {
            content: '';
            display: block;
            width: 400%;
            height: 100%;
            background-color: $dark;
            position: absolute;
            right: 30px;
            top: 0;
            z-index: 1;

            @include desktop {
                right: 0;
            }
        }
    }

    h3 {
        font-family: $font-bold;
        line-height: 49px;
        font-size: 30px;
        letter-spacing: 0.03em;
        color: $white;
        margin-bottom: 77px;
        position: relative;
        z-index: 5;

        @include tabletMedium {
            margin-bottom: 58px;
        }

        @include mobile {
            margin-bottom: 68px;
            font-size: 26px;
            line-height: 37px;
        }
    }

    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;
        z-index: 5;

        @include custom {
            flex-direction: column;
        }
    }

    &__item {
        width: calc(100%/4);
        position: relative;

        @include custom {
            width: 100%;
            margin-bottom: 45px;
            padding-left: 15px;

            &:last-of-type {
                margin-bottom: 0;
            }
        }

        span.seperated {
            position: absolute;
            width: 202px;
            top: 27px;
            right: 20px;
            height: 2px;
            background: url('../img/anti/line.svg') repeat-x;
            z-index: -1;

            @include desktopLow {
                width: 60%;
                right: 10px;
            }

            @include tablet {
                width: 50%;
            }

            @include custom {
                display: none;
            }
        }

        &:last-of-type {
            span {
                display: none;
            }
        }


        &--image {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            height: 56px;
            margin-bottom: 38px;

        }

        img {
            display: block;
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $white;
        }
    }
}





.combination {
    padding: 9.5em 0em;
    background-color: $white;

    @include tabletMedium {
        padding: 5em 0em 7.5em;
    }

    @include tabletLow {
        padding: 3em 0em 4em;
    }

    h3 {
        max-width: 825px;
        width: 100%;
        font-family: $font-bold;
        line-height: 56px;
        font-size: 40px;
        letter-spacing: 0.03em;
        color: $dark;
        margin-bottom: 70px;

        @include tabletMedium {
            margin-bottom: 40px;
            max-width: 100%;
        }

        @include tabletLow {
            font-size: 26px;
            line-height: 37px;
        }
    }

    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        @include desktop {
            justify-content: center;
            flex-direction: column;
        }
    }

    &__item {
        width: 47.5%;
        display: flex;
        position: relative;

        @include desktop {
            width: 100%;
            max-width: 570px;
            margin: 0 auto;
        }

        @include tabletLow {
            max-width: 100%;
            flex-direction: column;
        }

        &:first-of-type {
            margin-right: 5%;

            @include desktop {
                margin: 0px auto 60px;
            }
        }

        &:first-of-type .combination--image {
            &:before {
                content: '';
                display: block;
                width: 211px;
                height: 172px;
                background: url('../img/anti/comb-image-1.svg') no-repeat;
                background-size: cover;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: 1;

                @include tabletLow {
                    width: 150px;
                    height: 122px;
                }
            }
        }
    }

    &--image {
        width: 244px;
        margin-right: 36px;
        position: relative;

        @include tabletLow {
            width: 169px;
            margin-right: 0;

            img {
                width: 100%;
            }
        }

    }

    &--text {
        width: calc(100% - 280px);
        padding-top: 3em;

        @include desktop {
            padding-top: 2em;
        }

        @include tabletLow {
            width: 100%;
            padding-top: 0;
            margin-top: 32px;
        }

        b {
            display: block;
            font-family: $font-bold;
            line-height: 34px;
            font-size: 24px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 30px;
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;
        }
    }
}


.alerting {
    background-color: #F5F5F5;
    padding: 9.5em 0em;

    @include tabletMedium {
        padding: 5em 0em;
    }

    @include tabletLow {
        padding: 4em 0em;
    }

    h3 {
        font-family: $font-bold;
        line-height: 56px;
        font-size: 40px;
        letter-spacing: 0.03em;
        color: $dark;
        margin-bottom: 20px;

        @include tabletLow {
            font-size: 26px;
            line-height: 37px;
        }
    }

    p {
        max-width: 310px;
        width: 100%;
        font-family: $gt-reg;
        line-height: 30px;
        font-size: 16px;
        letter-spacing: 0.03em;
        color: $dark;
        margin-bottom: 54px;

        @include tabletLow {
            margin-bottom: 39px;
        }
    }

    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        @include desktop {
            flex-wrap: wrap;
        }

        @include tabletLow {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }

    &__item {
        width: calc(100%/3 - 31px);
        margin-right: 62px;
        position: relative;
        padding-top: 35px;
        padding-right: 10px;
        padding-bottom: 21px;
        padding-left: 52px;
        background-color: $white;
        min-height: 229px;

        &:last-of-type {
            margin-right: 0;
        }

        @include desktop {
            width: calc(100%/2 - 20px);
            margin-right: 40px;
            margin-bottom: 38px;

            &:nth-of-type(2) {
                margin-right: 0;
            }

        }

        @include tablet {
            min-height: 260px;
        }

        @include tabletLow {
            width: 100%;
            margin: 0px auto 32px;

            &:nth-of-type(2) {
                margin: 0px auto 32px;
            }
        }

        li {
            font-family: $gt-med;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;
            position: relative;
            margin-bottom: 10px;


            &:before {
                content: '';
                display: block;
                width: 4px;
                height: 4px;
                background: $accent;
                border-radius: 50%;
                position: absolute;
                left: -20px;
                top: 12px;
            }
        }

        h4 {
            font-family: $font-med;
            line-height: 34px;
            font-size: 30px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 19px;
        }

        img {
            display: block;
            position: absolute;
            right: -15px;
            top: -15px;
            z-index: 4;

            @include tabletLow {
                right: 15px;
            }
        }




    }
}

.fields-intro {
    position: relative;

    &--text {
        width: calc(100% - 620px);
        margin-right: 45px;
        padding-top: 8em;
        position: relative;

        @include tabletMedium {
            max-width: 575px;
            width: 100%;
            margin-right: 0;
            padding-top: 5em;
            position: absolute;
            left: 0;
            top: 0;
        }

        @media (max-width: 630px) {
            position: relative;
            max-width: 100%;
        }

        h1 {
            font-family: $font-bold;
            line-height: 79px;
            font-size: 56px;
            letter-spacing: 0.03em;
            color: $dark;
            margin-bottom: 30px;

            @include tabletMedium {
                font-size: 48px;
                line-height: 68px;
            }

            @include mobile {
                font-size: 32px;
                line-height: 45px;
            }
        }

        p {
            font-family: $gt-reg;
            line-height: 30px;
            font-size: 16px;
            letter-spacing: 0.03em;
            color: $dark;

            @include mobile {
                line-height: 28px;
            }
        }
    }


    .container {
        position: relative;
    }

    .back__link {
        left: 35px;

        @include desktopLow {
            top: 46px;
        }

        @include tabletMedium {
            top: 30px;
        }

        @include tablet {
            left: 25px;
        }
    }



    &__image {
        width: 575px;
        padding-right: 3em;
        padding-top: 7em;
        position: relative;


        @include tabletMedium {
            width: 100%;
            padding-top: 13em;
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            padding-right: 0;
        }


        @media (max-width: 630px) {
            justify-content: center;
            align-items: center;
            padding-top: 40px;
        }



        img {
            display: block;
            max-width: 100%;
            height: auto;
            margin: 0px auto;

            @include tabletMedium {
                width: 417px;
                margin: 0;
            }

            @include mobile {
                width: 288px;
            }
        }
    }
}


.page-id-564 .alerting__item,
.page-id-607 .alerting__item {
    height: auto;
    min-height: 300px;

    @include tablet {
        min-height: 300px;
    }
}
