.footer {
    padding: 2.5em 0em;
    position: relative;
    background: $dark;

    &.ru-custom {
        .footer__menu ul {
            @include tabletMedium {
                justify-content: flex-start;
            }

            @include mobile {
                flex-direction: column;

                align-items: flex-start;
            }
        }

        .footer__menu li {
            @include mobile {
                width: 100%;
            }

            @include tabletMedium {
                margin-bottom: 15px;
            }

            @include tablet {
                margin-bottom: 20px;
            }

            &:last-of-type {
                @include mobile {
                    margin-bottom: 0;
                }
            }
        }
    }

    &__inner {
        max-width: 1240px;
        width: 100%;
        margin: 0px auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;

        @include desktopLow {
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
        }

        @include desktopLow {
            padding: 0px 25px;
        }

        @include tabletLow {
            padding: 0px 15px;
        }
    }

    &__logo {
        @include desktopLow {
            margin-bottom: 24px;
        }

        @include tabletLow {
            display: none;
        }

        a {
            text-decoration: none;
            display: flex;
            justify-content: flex-start;
            align-items: center;



            &:focus {
                outline: none;
                border: none;
            }
        }

        div:first-of-type {
            margin-right: 30px;
        }

        div {
            color: $white;
        }
    }

    &__menu {
        @include desktopLow {
            // padding: 0em 1.5em;
            width: 100%;
        }

        @include tablet {
            padding: 0;
        }

        li {
            display: block;
            margin-right: 37px;

            @include tablet {
                width: calc(100%/2);
                margin: 0px 0px 20px;
            }
        }

        li:last-of-type {
            margin-right: 0px;
        }

        ul {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            @include desktopLow {
                justify-content: space-between;
            }

            @include tabletMedium {
                justify-content: space-between;
                flex-wrap: wrap;
            }
        }

        a {
            font-family: $font-reg;
            text-decoration: none;
            font-weight: 500;
            line-height: normal;
            font-size: 14px;
            letter-spacing: 0.07em;
            color: $white;
            position: relative;
            transition: all 0.4s;

            &:before {
                content: '';
                display: block;
                width: 0%;
                height: 2px;
                background-color: $accent;
                position: absolute;
                bottom: -10px;
                left: 0;
                transition: width 0.4s;
            }

            &:hover,
            &.is-active {
                color: $accent;

                &:before {
                    width: 100%;
                }
            }

        }
    }

    &.gray {
        background: #F5F5F5;

        .footer__menu a {
            color: $udark;

            &:hover {
                color: $accent;
            }
        }

        .footer__logo div {
            color: $udark;
        }
    }

    &.white {
        background: $white;

        .footer__menu a {
            color: $udark;

            &:hover {
                color: $accent;
            }
        }

        .footer__logo div {
            color: $udark;
        }
    }
}
