.form__fields {
    width: 100%;


    &--short {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        @include mobile {
            flex-direction: column;
            justify-content: center;
        }

        .form__fields--item {
            width: calc(100%/2 - 5%);

            @include mobile {
                width: 100%;
                margin: 0px auto 50px;
            }
        }

        .form__fields--item:first-of-type {
            margin-right: 10%;

            @include mobile {
                margin: 0px auto 50px;
            }
        }
    }

    &--long {
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;

        .form__fields--item {
            width: 100%;
        }
    }

    &--item {
        position: relative;
        margin-bottom: 50px;
    }

    label {
        display: block;
        font-family: $font-light;
        line-height: 20px;
        font-size: 12px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: $white;
        text-align: left;
        transition: all 0.4s;

        br {
            display: none;
        }
    }

    input {
        display: block;
        width: 100%;
        font-family: $font-bold;
        line-height: 30px;
        font-size: 20px;
        letter-spacing: 0.03em;
        color: $white;
        background: transparent;
        outline: none;
        box-shadow: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        position: relative;
        z-index: 1;

    }

    textarea {
        resize: none;
        display: block;
        width: 100%;
        font-family: $font-bold;
        line-height: 30px;
        height: 35px;
        font-size: 20px;
        letter-spacing: 0.03em;
        color: $white;
        background: transparent;
        border-width: 0px 0px 1px 0px;
        outline: none;
        box-shadow: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    }

    p.error {
        position: absolute;
        bottom: -25px;
        left: 0;
        font-size: 14px;
        line-height: 18px;
        color: $error;
    }

    button[type="submit"] {
        display: block;
        width: 178px;
        height: 47px;
        background: $accent;
        border-radius: 100px;
        outline: none;
        border: none;
        font-family: $font-bold;
        font-size: 12px;
        text-align: center;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: $white;
        transition: all 0.4s;

        br {
            display: none;
        }

        @include mobile {
            margin: 0px auto;
        }

        &:hover {
            background: $hovered;
        }
    }

    &.white-form {

        label,
        input,
        textarea {
            color: $some;
        }

        input,
        textarea {
            border-color: $some;
        }

        button[type="submit"] {
            background: $some;

            &:hover {
                background: $accent;
            }
        }
    }

    .input-field {
        position: relative;

        &:before {
            content: '';
            display: block;
            width: 0;
            height: 1px;
            background-color: $accent;
            position: absolute;
            left: 0;
            bottom: 0;
            transition: all 0.5s linear;
            z-index: 3;
        }

        &.active-field {
            label {
                color: $accent;
            }

            &:before {
                width: 100%;
            }

        }
    }
}
